<template>
  <div class="roles-permission">
  <div class="row">
  </div>
    <div class="row" style="margin-bottom:20px">
      <div class="col-md-6">
        <ul class="list-inline">
          <li>
              <div>
                <input type="checkbox" v-model="checkBox.selectAll" @click="selectAllCheckBox()">
              </div>
          </li>
               <li>
            <div class="btn-group">
            <button type="button" class="btn btn-default btn-sm">Ascending</button>
            <button type="button" class="btn btn-default btn-sm">Date</button>
            <button type="button" class="btn btn-default btn-sm">Name</button>
            <button type="button" class="btn btn-default btn-sm">Descending</button>
            </div>
          </li>
    
          <li>
          <div class="btn-magnify">
             <i class="ti-trash" style="font-size:25px; top:5px;left:5px;cursor:pointer"></i>
           </div>
          </li>
        </ul>
      </div>
      <div class="col-md-1 col-md-offset-5" style="margin-top:6px;">
        <ul class="list-inline">
       
            <li>
           <div class="btn-group">
              <button type="button" class="btn btn-default btn-sm" @click="openAddRolesModal()">Add Roles</button>
            </div>
          </li>
        </ul>
      </div>
    </div> <!--row-->
  
    <div class="row">
      <div class="col-md-12">
         <div>
        <div class="card">
        <div class="card-header">
          <h2 class="card-title">Set Permission</h2>
          <hr>
        </div>  
          <div style="padding:20px;margin:20px" class="table-responsive">
           <table class="table table-dark table-hover">
            <thead class="">
              <tr>
                <th style="width:15%">
                </th>
                <th  style="width:30%">Name</th>
                <th  style="width:30%">Description</th>
                <th style="width:30%">Operation</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(items,index) in roles.data">
                <td>
                <input type="checkbox" id="items.id" :value="items.RoleID" v-model="checkBox.selected">
                </td>
                <td>
                {{items.Name}}
                </td>
                <td>
                {{items.Description}}
                </td>
                <td>
                  <button class="btn " @click="openEditRolesModal(items)">Edit</button>
                  <button class="btn   " @click="deleteThisRolesModal('warning-message-and-cancel',items)">Delete</button>
                  <button class="btn " @click="openSetPermissionModal(items)">Set Permission</button>
                </td>
              </tr>
            </tbody>
          </table>
          </div>   
          </div> 
          </div>   <!--card-->
      </div>
    </div><!--row-->

    <!-- MODAL -->
    <sweet-modal  ref="setPermissionModal"  modal-theme="light" width="70%" style="margin-left:120px" blocking>
    <div class="row">
      <div class="col-md-11">
      <div class="row">
      <div class="col-md-12">
         <div>
        <div class="card">
        <div class="card-header">
          <h2 class="card-title">{{modalVariables.modalTitle}}</h2>
          <hr>
        </div>  
          <div style="padding:20px;margin:20px" class="table-responsive text-left">
          <table class="table table-bordered table-hover">
            <thead>
              <tr>
                <th>Module Name</th>
                <th>Create</th>
                <th>Read</th>
                <th>Update</th>
                <th>Delete</th>
                <th>View</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(items,index) in sideBarData">
                <td>
                 <i :class="items.icon"></i> {{items.name}}
                </td>
                <td>
                  <input id="items.name" type="checkbox" :value="items.name" v-model="checkBoxEditRoles.create">
                </td>
                 <td>
                  <input id="items.name" type="checkbox" :value="items.name" v-model="checkBoxEditRoles.read">
                </td>
                  <td>
                  <input id="items.name" type="checkbox" :value="items.name" v-model="checkBoxEditRoles.update">
                </td>
                <td>
                  <input id="items.name" type="checkbox" :value="items.name" v-model="checkBoxEditRoles.delete">
                </td>
                  <td>
                  <input id="items.name" type="checkbox" :value="items.name" v-model="checkBoxEditRoles.view">
                </td>
              </tr>
            </tbody>
          </table>
          </div>   
          </div> 
          <div class="card-footer text-right">
            <button class="btn btn-fill btn-wd" @click="saveThisRolesModal('warning-message-and-cancel')">Save</button>
          </div>

   
          </div>   <!--card-->
      </div>
    </div>
      </div> 
    </div>
  </sweet-modal>

  <sweet-modal  ref="addRolesModal"  modal-theme="light" width="50%" blocking>
    <div class="card" style="margin-top: 30px; box-shadow:none">
      <div class="card-header" style="text-align: left;">
          <h4 class="card-title" style="margin-bottom: 20px"> Add Roles</h4>
        <div class="form-group">
          <input id="addRolesModal" type="text" placeholder="Enter Keyword..." class="form-control" v-model="addRolesFields.name" autofocus required="">
        </div>
      </div>
      <div class="card-content">
      <div class="form-group">
       <textarea rows="5" class="form-control border-input"
        placeholder="Enter Description" v-model="addRolesFields.description">    
        </textarea>
        </div>
      </div>
        <div class="card-footer">
        </div>
      </div>
    <div  style="float:right">
    <button class="btn btn-md btn-fill btn-primary" @click="saveThisAddRolesModal()">Save</button>
    </div>
  </sweet-modal>

  <sweet-modal  ref="editRolesModal"  modal-theme="light" width="50%" blocking>
    <div class="card" style="margin-top: 30px; box-shadow:none">
      <div class="card-header" style="text-align: left;">
          <h4 class="card-title" style="margin-bottom: 20px">Edit Roles</h4>
        <div class="form-group">
          <input type="text" placeholder="Enter Keyword..." class="form-control a" v-model="editRolesFields.name" required autofocus>
        </div>
      </div>
      <div class="card-content">
      <div class="form-group">
       <textarea rows="5" class="form-control border-input"
        placeholder="Enter Description" v-model="editRolesFields.description">    
        </textarea>
        </div>
      </div>
        <div class="card-footer">
        </div>
      </div>
    <div  style="float:right">
    <button class="btn btn-md btn-fill btn-primary" @click="alertEditMessage()">Save</button>
    </div>
  </sweet-modal>
  </div>
</template>
<script>

import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
import links from '../../../../sidebarLinks.js'
import swal from 'sweetalert2'
import axios from 'axios'

  export default {
  components:{
  'sweet-modal':SweetModal,
  'sweet-modal-tab': SweetModalTab
    },
    props:  ['sidebarlinks'],
    data () {
      return {
        sideBarData:this.sidebarLinks,

        modalVariables :{
         modalTitle: '',
         modalID: '',
         modelItems : {}
        },
        addRolesFields:{
          name:'',
          description:''
        },
        deleteRolesFields:{
          id:'',
        },
        editRolesFields:{
          id:'',
          name:'',
          description:''
        },
        checkBox:{
          selected:[],
          selectAll:false
        },
        checkBoxEditRoles:{
          create:[],
          view:[],
          read:[],
          update:[],
          delete:[], 
        },
        roles: {
          data: {}
        }
        
      }
    },
    methods: {
    async editThisRole(){
      const data = {
        "query":"mutation($input:roleFields,$RoleID:String!) { UpdateRole( input:$input,RoleID:$RoleID ) { message,errors { Path,Message } } }",
        "variables":{
          "RoleID":this.editRolesFields.id,
          "input":{
            "name":this.editRolesFields.name,
            "description":this.editRolesFields.description
          }
        }
      } 
      return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log(response)
        if(response.data.UpdateRole.errors){
          return true
        }else{
          return false
        }

        }).catch((err) => {
         console.log(err);
        });
     },
     async deleteThisRole(){
      const data = {
        "query":"mutation($RoleID:String!) { DeleteRole (RoleID:$RoleID ) { message,errors { Path,Message } } }",
        "variables":{
          "RoleID": this.deleteRolesFields.id
        }
      } 
        return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log(response)
        if(response.data.DeleteRole.errors == null){
          return true
        }else{
          return false
        }
        }).catch((err) => {
         console.log(err);
        });

     },
     async saveThisNewRole(){
        const data = {
          "query":"mutation($input:roleFields!) { NewRole( input:$input ) { message,errors { Path,Message } } }",
          "variables":{
            "input":{
              "name":this.addRolesFields.name,
              "description":this.addRolesFields.description
            }
          }
        }
        return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log(response)
        if(response.data.NewRole.errors.length <= 0){
          return true
        }else{
          return false
        }

        }).catch((err) => {
         console.log(err);
        });

      },
      pushThisItemsInsideModal(items){
        if(items.Create.length <= 0){
          // console.log("create is empty")
        }else{
          // console.log("create is not empty")
          for(var i = 0; i < items.Create.length; i++){
            if(items.Create[i] == "roles_permission"){
              this.checkBoxEditRoles.create.push("Roles And Permission")
            }else{
              var filterFirstLetters = items.Create[i].substring(0, 1);
              // console.log("First Letter: ",filterFirstLetters)
              var filterRemainingLetters = items.Create[i].substring(1)
              // console.log("Remaining Letter: ",filterRemainingLetters)
              var makeFirstLetterUpper = filterFirstLetters.toUpperCase()
              var finalWord = makeFirstLetterUpper + filterRemainingLetters

              this.checkBoxEditRoles.create.push(finalWord) 
            }
          }
        }//create 

        if(items.Read.length <= 0){
          // console.log("read is empty!")
        }else{
          for(var i = 0; i < items.Read.length; i++){
            if(items.Read[i] == "roles_permission"){
              this.checkBoxEditRoles.read.push("Roles And Permission")
            }else{
              var filterFirstLetters = items.Read[i].substring(0, 1);
              var filterRemainingLetters = items.Read[i].substring(1)
              var makeFirstLetterUpper = filterFirstLetters.toUpperCase()
              var finalWord = makeFirstLetterUpper + filterRemainingLetters
              this.checkBoxEditRoles.read.push(finalWord) 
            }
          }
        }//read 

        if(items.Update.length <= 0){

        }else{
          for(var i = 0; i < items.Update.length; i++){
            if(items.Update[i] == "roles_permission"){
              this.checkBoxEditRoles.update.push("Roles And Permission")
            }else{
              var filterFirstLetters = items.Update[i].substring(0, 1);
              var filterRemainingLetters = items.Update[i].substring(1)
              var makeFirstLetterUpper = filterFirstLetters.toUpperCase()
              var finalWord = makeFirstLetterUpper + filterRemainingLetters
              this.checkBoxEditRoles.update.push(finalWord) 
            }
          }
        }//update 

        if(items.Delete.length <= 0){
          // console.log("view is empty!")

        }else{
          for(var i = 0; i < items.Delete.length; i++){
            if(items.Delete[i] == "roles_permission"){
              this.checkBoxEditRoles.delete.push("Roles And Permission")
            }else{
              var filterFirstLetters = items.Delete[i].substring(0, 1);
              var filterRemainingLetters = items.Delete[i].substring(1)
              var makeFirstLetterUpper = filterFirstLetters.toUpperCase()
              var finalWord = makeFirstLetterUpper + filterRemainingLetters
              this.checkBoxEditRoles.delete.push(finalWord) 
            }
          }
        }//delete 

        if(items.View.length <= 0){
          // console.log("view is empty!")
        }else{
          for(var i = 0; i < items.View.length; i++){
            if(items.View[i] == "roles_permission"){
              // this.checkBoxEditRoles.view.push("Roles And Permission")
            }else{
              var filterFirstLetters = items.View[i].substring(0, 1);
              var filterRemainingLetters = items.View[i].substring(1)
              var makeFirstLetterUpper = filterFirstLetters.toUpperCase()
              var finalWord = makeFirstLetterUpper + filterRemainingLetters

              this.checkBoxEditRoles.view.push(finalWord) 
            }
          }
        }//view 
      },
      getUpdatedItemsInPermission(){
        console.log("this old id: ", this.modalVariables.modalID)
        for(var i = 0; i < this.roles.data.length; i++){
            if(this.modalVariables.modalID == this.roles.data[i].RoleID){
              this.modalVariables.modalItems = this.roles.data[i]
            }
        }
      },
      openSetPermissionModal(items){
        console.log("open modal this")
        this.$refs.setPermissionModal.open()
        this.modalVariables.modalTitle = items.Name
        this.modalVariables.modalID = items.RoleID
        this.modalVariables.modalItems = items
        this.checkBoxEditRoles.create = []
        this.checkBoxEditRoles.read = []
        this.checkBoxEditRoles.update = []
        this.checkBoxEditRoles.delete = []
        this.checkBoxEditRoles.view = []

        this.pushThisItemsInsideModal(items)

        console.log("modal items",  this.modalVariables.modalItems)


      },
      openAddRolesModal(){
        this.$refs.addRolesModal.open()
      },
      openEditRolesModal(items){
        this.editRolesFields.id = items.RoleID
        this.editRolesFields.name = items.Name
        this.editRolesFields.description = items.Description
        this.$refs.editRolesModal.open()

        // console.log("id", this.editRolesFields.id)
        // console.log("name", this.editRolesFields.name)
        // console.log("description", this.editRolesFields.description)

      },
      setThisNavObject(props){
       this.sideBarData =  props
       // console.log(props)
      },
      selectAllCheckBox(){
        this.checkBox.selected = []
        if(!this.checkBox.selectAll){
            for(let i in this.roles.data){
              this.checkBox.selected.push(this.roles.data[i].RoleID)
            }
        }
      },


      async updateRolesPermission(){
        if(this.checkBoxEditRoles.create.length <= 0 )
        {
          // console.log("checkBoxEditRoles.create is empty")
        }else{
          // console.log("checkBoxEditRoles.create not empty")
          for(var i = 0; i < this.checkBoxEditRoles.create.length; i++){
            if(this.checkBoxEditRoles.create[i] === "Roles And Permission"){
              // console.log("found it!")
              this.checkBoxEditRoles.create[i] = "roles_permission"
            }else{
            var convertThisArray = this.checkBoxEditRoles.create[i].toLowerCase()
            this.checkBoxEditRoles.create[i] = convertThisArray
            }
          }
        }

        if(this.checkBoxEditRoles.read.length <= 0 )
        {
          // console.log("checkBoxEditRoles.read is empty")
        }else{
          // console.log("checkBoxEditRoles.read not empty")
          for(var i = 0; i < this.checkBoxEditRoles.read.length; i++){
            if(this.checkBoxEditRoles.read[i] === "Roles And Permission"){
              // console.log("found it!")
              this.checkBoxEditRoles.read[i] = "roles_permission"
            }else{
            var convertThisArray = this.checkBoxEditRoles.read[i].toLowerCase()
            this.checkBoxEditRoles.read[i] = convertThisArray
            }
          }
        }

        if(this.checkBoxEditRoles.update.length <= 0 )
        {
          // console.log("checkBoxEditRoles.update is empty")
        }else{
          // console.log("checkBoxEditRoles.update not empty")
          for(var i = 0; i < this.checkBoxEditRoles.update.length; i++){
  
            if(this.checkBoxEditRoles.update[i] === "Roles And Permission"){
              // console.log("found it!")
              this.checkBoxEditRoles.update[i] = "roles_permission"
            }else{
            var convertThisArray = this.checkBoxEditRoles.update[i].toLowerCase()
            this.checkBoxEditRoles.update[i] = convertThisArray
            }
          }
        }

        if(this.checkBoxEditRoles.delete.length <= 0 )
        {
          // console.log("checkBoxEditRoles.delete is empty")
        }else{
          // console.log("checkBoxEditRoles.delete not empty")

          for(var i = 0; i < this.checkBoxEditRoles.delete.length; i++){
  
            if(this.checkBoxEditRoles.delete[i] === "Roles And Permission"){
              // console.log("found it!")
              this.checkBoxEditRoles.delete[i] = "roles_permission"
            }else{
            var convertThisArray = this.checkBoxEditRoles.delete[i].toLowerCase()
            this.checkBoxEditRoles.delete[i] = convertThisArray
            }
          }
        }


        if(this.checkBoxEditRoles.view.length <= 0 )
        {
          // console.log("checkBoxEditRoles.delete is empty")
        }else{
          // console.log("checkBoxEditRoles.delete not empty")

          for(var i = 0; i < this.checkBoxEditRoles.view.length; i++){
  
            if(this.checkBoxEditRoles.view[i] === "Roles And Permission"){
              // console.log("found it!")
              this.checkBoxEditRoles.view[i] = "roles_permission"
            }else{
            var convertThisArray = this.checkBoxEditRoles.view[i].toLowerCase()
            this.checkBoxEditRoles.view[i] = convertThisArray
            }
          }
        }
        // console.log("done coverting create :",this.checkBoxEditRoles.create)
        // console.log("done coverting read :",this.checkBoxEditRoles.read)
        // console.log("done coverting delete :",this.checkBoxEditRoles.delete)
        // console.log("done coverting update :",this.checkBoxEditRoles.update)
        // console.log("done coverting view :",this.checkBoxEditRoles.view)

        const data = {
          "query":"mutation($input:PermissionFields,$RoleID:String!) { UpdateRolePermission( input:$input,RoleID:$RoleID ) { message,errors { Path,Message } } }",
            "variables":{
              "RoleID":this.modalVariables.modalID,
              "input":{
                "read":this.checkBoxEditRoles.read,
                "delete":this.checkBoxEditRoles.delete,
                "update":this.checkBoxEditRoles.update,
                "view":this.checkBoxEditRoles.view,
                "create":this.checkBoxEditRoles.create,
              }
            }
        }
        console.log("const:" ,data)

       return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log("response: ",response)
        if(response.data.UpdateRolePermission.errors.length <= 0){
          return true
        }else{
          false
        }

        }).catch((err) => {
           console.log(err);
          });
      },

      async getRolesPermission(){
        const data = {
           "query":"query{ roles { data{ RoleID,Name,Description,Read,Update,Create,Delete,View,Created },page { items,pageNo,totalPages } ,sortableFields } }"
        }
        return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        this.roles.data = response.data.roles.data
        if(response.status == 200){
        console.log("roles data: ",  this.roles.data)
          return true
        }else{
          return false
        }
        }).catch((err) => {
         console.log(err);
        });
      },
      deleteThisRolesModal (type,items) {
        console.log("delete:",items.RoleID)
        this.deleteRolesFields.id = items.RoleID

          if (type === 'warning-message-and-cancel') {
          swal({
            title: 'Are you sure?',
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes',
            cancelButtonText: 'Cancel',
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            buttonsStyling: false
          }).then(async () => {
            if(await this.deleteThisRole())
            {
              console.log("no errors found")
              this.getRolesPermission()

            swal({
              title: 'Role Deleted!',
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            });

            }else{
              console.log("errors found")
              swal({
                title: 'Something went wrong!',
                type: 'warning',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
 
          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              swal({
                title: 'Cancelled',
                type: 'error',
                confirmButtonClass: 'btn btn-info btn-fill',
                buttonsStyling: false
              })
            }
          })
        }
      },
      saveThisRolesModal (type) {
          if (type === 'warning-message-and-cancel') {
          swal({
            title: 'Are you sure?',
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes',
            cancelButtonText: 'Cancel',
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            buttonsStyling: false
          }).then(async () => {
            if(await this.updateRolesPermission())
            {
              console.log("no errors found")
              if(await this.getRolesPermission()) {
                console.log("no error")
                this.getUpdatedItemsInPermission()
                 this.pushThisItemsInsideModal(this.modalVariables.modalItems)        

              }else{
                console.log("error")
              }
            swal({
              title: 'Role Updated!',
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            });

            }else{
              console.log("errors found")
              swal({
                title: 'Fields Cannot Be Empty',
                type: 'warning',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
 
          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              swal({
                title: 'Cancelled',
                type: 'error',
                confirmButtonClass: 'btn btn-info btn-fill',
                buttonsStyling: false
              })
            }
          })
        }
      },
     async saveThisAddRolesModal () {
            if(await this.saveThisNewRole())
            {
              console.log("no errors found")
              this.getRolesPermission()
              this.addRolesFields.name = ""
              this.addRolesFields.description = ""
            swal({
              title: 'Role Added!',
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            });
               this.$refs.addRolesModal.close()

            }else{
              console.log("errors found")
              swal({
                title: 'Role Already Exist!',
                type: 'warning',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
          }, 
      async alertEditMessage(){
      if(await this.editThisRole())
        {
         console.log("errors found")
        swal({
          title: 'Something went wrong!',
          type: 'warning',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false
        })


            }else{
                         console.log("no errors found")
              this.getRolesPermission()
            swal({
              title: 'Role Added!',
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false }).then(()=>{
                     this.$refs.editRolesModal.close()
              })
            }
        
      }
    },
    mounted(){
    this.getRolesPermission();
    this.setThisNavObject(JSON.parse(JSON.stringify(links)))
    }
  }
</script>
<style>
</style>



