// 
import jwtDecode from 'jwt-decode'; 
export default {

	data() {
		//api
    	return {
    	//process.env.ROOT_API
    		post_url: process.env.ROOT_API,
    		post_config:{
    			"headers":{
                    "Content-Type":"application/json"
                }
    		},
    		post_config_auth:{
    			"headers":{
            "Content-Type":"application/json",
            "Authorization":window.localStorage.getItem("session")
          }
    		}
        }
   	},
	methods:{
		validate_session(){
		  // console.log("called this validate_session!");
	      const dateNow = new Date();
	      const sessiondata =  window.localStorage.getItem("session");
	      if(!sessiondata){
		  	this.$router.push('/login')
	      	return null
	      }else{
	        const decoded = jwtDecode(sessiondata);
	        if((dateNow.getTime()/1000) <= decoded.exp){
	        		return true;

	        	
	        }else{
	        	return false;
	        }
	      }
	    },
	    logout(){
	    	// console.log("called logout!");
	    	window.localStorage.removeItem("session");
	    	this.validate_session();
	    },
        refreshThis(){
    	this.$router.go(this.$router.currentRoute)
   		},
   	convertDate(date_var){
    var d = new Date(date_var),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear(),
        today = '' + d.getDay().toString();

    var thisDay = "";
        if(today == "1" ){
          thisDay = "Mon"
        }else if(today == "2"){
          thisDay = "Tue"
        }else if(today == "3"){
          thisDay = "Wed"
        }else if(today == "4"){
          thisDay = "Thur"
        }else if(today == "5"){
          thisDay = "Fri"
        }else if(today == "6"){
          thisDay = "Sat"
        }
         else {
          thisDay = "Sun"
        }
    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return month + "/"+ day + "/"+ year + " ";
  },

      convertDateDash(date_var){
    var d = new Date(date_var),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear(),
        today = '' + d.getDay().toString();

    var thisDay = "";
        if(today == "1" ){
          thisDay = "Mon"
        }else if(today == "2"){
          thisDay = "Tue"
        }else if(today == "3"){
          thisDay = "Wed"
        }else if(today == "4"){
          thisDay = "Thur"
        }else if(today == "5"){
          thisDay = "Fri"
        }else if(today == "6"){
          thisDay = "Sat"
        }
         else {
          thisDay = "Sun"
        }
    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return   day + "-"+ month + "-"+ year;
  }
	},
	mounted(){
		// alert(process.env.lester);
	}
}


