  //RANDOM
  convertDate(date_var){
  var d = new Date(date_var),
  month = '' + (d.getMonth() + 1),
  day = '' + d.getDate(),
  year = d.getFullYear(),
  today = '' + d.getDay().toString();
  var thisDay = "";
  if(today == "1" ){
    thisDay = "Mon"
  }else if(today == "2"){
    thisDay = "Tue"
  }else if(today == "3"){
    thisDay = "Wed"
  }else if(today == "4"){
    thisDay = "Thur"
  }else if(today == "5"){
    thisDay = "Fri"
  }else if(today == "6"){
    thisDay = "Sat"
  }else{
    thisDay = "Sun"
  }
  if (month.length < 2) month = '0' + month;
  if (day.length < 2) day = '0' + day;
  return month + "/"+ day + "/"+ year;
  },

