    var strsplit = str.split(' ')
    if(strsplit.length == 1){
     return strsplit[0]
    }else{
      var combinestr = strsplit[0] + " " + strsplit[1] + " " + strsplit[2] 
      // console.log(combinestr)
    return combinestr
    }
