<template>
  <div class="token-set">

       <div class="row">
      <div class="col-md-12">
        <div class="card"  style="box-shadow:none">
         <div class="card-header">
          <div class="row">
            <div class="col-md-4">
               <h2 class="card-title">Contact Us</h2>
            </div>
            <div class="col-md-4">
            </div>
            <div class="col-md-4">
              <button class="btn btn-sm" style="float:right;margin-right: 10px;" @click="refreshData()">Refresh</button>
              <button class="btn btn-sm" style="float:right;margin-right: 5px;" @click="openAddUserModal()">Add</button>
            </div>
          </div>
          <!-- ROW -->
          </div>
          <div class="row">
            <div class="card-header">
              <div class="form-group">
                <div class="col-md-2">
                  <select class="form-control">
                    <option>Email</option>
                    <option>User</option>
                  </select>
                </div>
                 <div class="col-md-8">
                  <div class="input-group">
                     <input type="text" class="form-control" placeholder="Please Input" autofocus="true">
                     <span class="input-group-addon"><i class="ti-search"></i></span>
                  </div>
                </div>
                <div class="col-md-1">
                  <button class="btn btn-fill btn-info" >Reset</button>
                </div>
              </div>
              </div>
          </div>
          <hr style="margin-top: 12px;">
          <!-- ROW -->
          <div style="padding:10px;margin:10px" class="table-responsive">
           <table class="table table-striped table-bordered table-hover">
            <thead class="">
              <tr>
                 <th ><span style="cursor:pointer">Date Created <i style="" :class="(sortableFields.icon.dateCreated) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                 <th ><span  @click="sortData('email')"  style="cursor:pointer">Email <i style="" :class="(sortableFields.icon.email) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                 <th ><span  @click="sortData('fullname')"  style="cursor:pointer">Full Name <i style="" :class="(sortableFields.icon.fullname) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                 <th >Mobile Number</th>
                 <th >Message</th>
                <th >Operation</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(items,index) in getContactUsFields.data">
                <td>
                  No Data Yet
                </td>
                <td>
                  {{items.email}}
                </td>
                <td>
                  {{convertFullname(items.fullname)}}
                </td>
                <td >
                  {{items.mobilenumber}}
                </td>
                <td>
                  {{items.message}}
                </td>
                <td>
                  <button class="btn btn-sm" @click="openContactUsPreviewModal(items)">View</button>
                  <button class="btn btn-sm" @click="deleteContactUsModal(items)" >Delete</button>
                </td>
              </tr>
            </tbody>
          </table>
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-3"></div>
              <div class="col-md-2"></div>
               <div class="col-md-1"  style="float:right">
                <div class="form-group">
                  <select class="form-control" @change="getUser()" v-model="sortableFields.limit.pageLimit">
                    <option>7</option>
                    <option>20</option>
                    <option>30</option>
                    <option>50</option>
                    <option>80</option>
                  </select>
                  </div>
               </div>
                  <div class="col-md-3" >
               <div class="btn-group" style="float:right;margin-top: 7px;">
                <button type="button" class="btn btn-default  btn-sm btn-move-left" @click="sortableFields.limit.pageNumber > 1 ? sortableFields.limit.pageNumber-- : 1,getUser()">
                  <span class="btn-label"> <i class="ti-angle-left"></i> </span>
                </button>
                 <button type="button" class="btn btn-default  btn-sm btn-move-left">
                  <span class="btn-label">{{sortableFields.limit.pageNumber}}</span>
                </button>
                <button type="button" class="btn btn-default btn-sm btn-move-right" @click="sortableFields.limit.pageNumber++,getUser()">
                  <span class="btn-label"> <i class="ti-angle-right"></i></span>
                </button>
               </div>
              </div>
          </div>
          </div>   
      </div>
        <!-- Card -->
      </div>
      <!-- Column  -->
    </div>

    <!-- Modal -->
      <sweet-modal ref="contactUsPreviewModal"  modal-theme="light">
      <div class="card" style="box-shadow:none">
    <div class="card-header">
      <h4 class="title">View User Profile</h4>
    </div>
    <div class="card-content">
      <form>
          <div class="row">
          <div class="col-md-6">
            <fg-input type="text"
                      label="Date Created" 
                      placeholder="Date Created" 
                      v-model="previewContactUsFields.dateCreated"
                      disabled>
            </fg-input>
          </div>
          <div class="col-md-6">
          <fg-input type="email"
                    label="Email"
                    placeholder="Email" 
                    v-model="previewContactUsFields.email"
                    disabled>
          </fg-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <fg-input type="text"
                      label="Full Name" 
                      placeholder="Full Name" 
                      v-model="previewContactUsFields.fullname"
                      disabled>
            </fg-input>
          </div>
          <div class="col-md-6">
          <fg-input type="email"
                    label="Mobile Number"
                    placeholder="Mobile Number" 
                    v-model="previewContactUsFields.mobileNumber"
                    disabled>
          </fg-input>
          </div>
        </div>

        <div class="row">
            <div class="col-md-12">
              <label>Message</label>
             <div class="form-group">
               <textarea rows="5" class="form-control border-input"
               v-model="previewContactUsFields.message"
                placeholder="Enter Description" disabled>    
                </textarea>
              </div>
          </div>
        </div>   

      </form>
    </div>
  </div>
  </sweet-modal>
</div>
</template>
<script>
  import axios from 'axios'
  import swal from 'sweetalert2'
  import { SweetModal, SweetModalTab } from 'sweet-modal-vue'

  import Vue from 'vue'
  import {Table, TableColumn} from 'element-ui'
  Vue.use(Table)
  Vue.use(TableColumn)
  export default {
    components:{
      'sweet-modal':SweetModal,
      'sweet-modal-tab': SweetModalTab,
    },


    data () {
      return {
         sortableFields:{
            fieldsToSort: 'email', 
          icon:{
            dateCreated:true,
            email:true,
            fullname:true,
            message:true
          },
            sort:{
              field:'asc' 
          },
          limit:{
              pageLimit:'7',
              pageNumber:'1',
              lastIdNumber:''
          },
         },
         getContactUsFields:{
          data:''
         },
         deleteContactUsFields:{
          array:[]
         },
         previewContactUsFields:{
           dateCreated:'',
           email:'',
           fullname:'',
           mobileNumber: '',
           message:''
         }

      }
    },
    methods: {
    //SORT 
       sortData(items){
        this.sortableFields.fieldsToSort = items

        switch (this.sortableFields.fieldsToSort) {
              case 'email':
                    if(this.sortableFields.icon.email){
                      this.sortableFields.icon.email = false
                      this.sortableFields.sort.field = 'desc'
                      this.getContactUs()
                    }else{
                      this.sortableFields.icon.email = true
                      this.sortableFields.sort.field = 'asc'
                      this.getContactUs()
                    }
                  break;
              case 'fullname':
                      if(this.sortableFields.icon.fullname){
                      this.sortableFields.icon.fullname = false
                      this.sortableFields.sort.field = 'desc'
                      this.getContactUs()
                    }else{
                      this.sortableFields.icon.fullname = true
                      this.sortableFields.sort.field = 'asc'
                      this.getContactUs()
                    }
                  break;
              default:
                  break;
          }
      },
    //CONVERTER

    convertFullname(items){
      console.log(items)
        if(items == null){
          return "No Data Found"
        }else{
          return items
        }
    },

    //CRUD
    getContactUs(){
      const data = {
        "query":"query($limit:limit,$sort:sort){ReadMessages(limit:$limit,sort:$sort){data{id,email,fullname,firstname,lastname,company,mobilenumber,subject,message,address,qq,skype},page{items,pageNo,totalPages},sortableFields}}",
        "variables":{
          "sort":{
            "field":this.sortableFields.fieldsToSort,
            "by":this.sortableFields.sort.field
          },
          "limit":{
          "to":"0",
          "page":"1",
          "last_id":""
          }
        }
      }
      axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("getContactUs: ", response)
      this.getContactUsFields.data = response.data.ReadMessages.data
      }).catch((err) => {
        console.log(err);
      });
    },

    async deleteContactUs(){
    const data = {
      "query":"mutation($input:[String!]!) { DeleteMessage( input:$input ) { message,errors { Path,Message } } }",
      "variables":{
        "input":this.deleteContactUsFields.array
      }
    }
    return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    console.log("deleteContactUs: ", response)
    if(response.data.errors){
      return true
    }else{
      return false
    }
    }).catch((err) => {
      console.log(err);
    });
    },

    //MODAL

    async deleteContactUsModal(items){
      this.deleteContactUsFields.array.push(items.id)
      console.log(items)
      swal({
        title: 'Are you sure?',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        buttonsStyling: false
      }).then(async () => {
      if(await this.deleteContactUs())
      {
        swal({
          title: 'Something went wrong!',
          type: 'warning',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false
        })
      }else{
       swal({
          title: 'News Deleted!',
          type: 'success',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false
        }).then(()=>{
          this.getContactUs()
        })
        }
      }, function (dismiss) {
        // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
        if (dismiss === 'cancel') {
          swal({
            title: 'Cancelled',
            type: 'error',
            confirmButtonClass: 'btn btn-info btn-fill',
            buttonsStyling: false
          })
        }
      })
    },

      //OPEN MODAL

  openContactUsPreviewModal(items){
   console.log("open preview: ", items)
   this.previewContactUsFields.dateCreated = "No Data Found"
   this.previewContactUsFields.email = items.email
   this.previewContactUsFields.fullname = this.convertFullname(items.fullname) 
   this.previewContactUsFields.mobileNumber = items.mobilenumber
   this.previewContactUsFields.message = items.message
   this.$refs.contactUsPreviewModal.open()
  }

  //CLOSE MODAL

 

  },


   
   mounted(){
    this.getContactUs()
   }
  }
</script>
<style>
</style>



