<template>
  <div class="card">
    <div class="card-header">
      <h4 class="title">Edit Profile</h4>
    </div>
    <div class="card-content" style="min-height:500px">
      <form>
        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="Username" 
                      placeholder="Username" 
                      v-model="userProfilePage.data.Username"
                      disabled="true">
            </fg-input>
          </div>
          <div class="col-md-4">
          <fg-input type="email"
                    label="Email"
                    placeholder="Email" 
                    v-model="userProfilePage.data.Email">
          </fg-input>
        
          </div>
          <div class="col-md-4">
            <fg-input type="password"
                      label="Password"
                      placeholder="password"
                      value="password">
          </fg-input>
          <button type="submit" class="btn btn-info btn-fill btn-xs" style="float:right">
          Change
          </button>
          </div>
        </div>

        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="First Name"
                      placeholder="First Name"
                      v-model="userProfilePage.data.Firstname">
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Last Name"
                      placeholder="Last Name"
                      v-model="userProfilePage.data.Lastname">
            </fg-input>
          </div>
            <div class="col-md-4">
            <div class="form-group">
              <label>Birthdate</label> <br/>
              <el-date-picker  type="date" placeholder="Pick a day" :picker-options="pickerOptions1"  v-model="userProfilePage.data.Birthdate">
              </el-date-picker>
            </div>
          </div>
        </div>


          <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="City"
                      placeholder="City"
                      v-model="userProfilePage.address.City">
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Country"
                      placeholder="Country"
                      v-model="userProfilePage.address.Country">
                     >
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Street"
                      placeholder="Street"
                      v-model="userProfilePage.address.Street">
                      >
            </fg-input>
          </div>
        </div>

        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>About Me</label>
              <textarea rows="5" class="form-control border-input"
                        placeholder="Here can be your description"
                      
                       >
              </textarea>
            </div>
          </div>
        </div>
        <div class="text-center">
          <button type="submit" class="btn btn-info btn-fill btn-wd" @click="saveThisModalValidation('warning-message-and-cancel')">
            Update Profile
          </button>
        </div>
        <div class="clearfix"></div>
      </form>
    </div>
  </div>
</template>
<script>

import jwtDecode from 'jwt-decode'; 
import Datepicker from 'vuejs-datepicker';
import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
import axios from 'axios'
import swal from 'sweetalert2'


  export default {
    components:{
     [DatePicker.name]: DatePicker

    },
    data () {

      return {
        validation:{
          checkEmail:''
        },
        userProfilePage: {
          data :{},
          address:{}
        },
          pickerOptions1: {
            shortcuts: [{
              text: 'Today',
              onClick (picker) {
                picker.$emit('pick', new Date())
              }
            },
            {
              text: 'Yesterday',
              onClick (picker) {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24)
                picker.$emit('pick', date)
              }
            },
            {
              text: 'A week ago',
              onClick (picker) {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
                picker.$emit('pick', date)
              }
            }]
          },
      }
    },
    methods: {
      checkEmail(){
        console.log("pacheck be")
        this.validation.checkEmail = this.userProfilePage.data.Email
        console.log("Email set: ", this.validation.checkEmail)
      },
      getUserProfileData(){
        const data = {
           "query":"query{ Profile{UserID,Username,Email,Roles,Firstname,Lastname,Birthdate,Language,Created,Address { Country,City,Street } } }"
        }
        axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        this.userProfilePage.data = response.data.Profile
        this.userProfilePage.address = response.data.Profile.Address
        console.log("data: ",    this.userProfilePage.data)
        this.checkEmail()

          }).catch((err) => {
           console.log(err);
          });
      },
  
      async updateUserProfileData(){
        var birthdateVar =  this.convertDateDash(this.userProfilePage.data.Birthdate)
        var birthdateTosTring = birthdateVar.toString()
        // console.log( typeof birthdateTosTring)
        // console.log(  birthdateTosTring)
        const data = {
            "query":"mutation($input:ProfileFields!,$password:String!) { SaveProfile( input:$input,password:$password ) { message,errors {Path,Message} } }",
            "variables":{
              "password":"myadmin",
              "input":{
                "birthdate": birthdateTosTring,
                "language": this.userProfilePage.data.Language,
                "firstname":this.userProfilePage.data.Firstname,
                "lastname": this.userProfilePage.data.Lastname,
                "address" :{
                  "street" : this.userProfilePage.address.Street,
                  "country" :  this.userProfilePage.address.Country,
                  "city" : this.userProfilePage.address.City
                }
              }
            }
        }
       return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
          console.log("response: ",response)
          if(response.data.SaveProfile.errors)
          {
            return true
          }else{
            return false
          }

          });
      },

             saveThisModalValidation (type) {
          if (type === 'warning-message-and-cancel') {
          swal({
            title: 'Are you sure?',
            text: 'This file will be edited',
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, edit it!',
            cancelButtonText: 'No, keep it',
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            buttonsStyling: false
          }).then( async() => {

          if(await this.updateUserProfileData())
              {
                  swal({
                text: 'Email already exist',
                type: 'warning',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              });
              }else{
              swal({
                text: 'Yeahhhhhhhhh! File Edited.',
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              });

              }

  

          }, function (dismiss) {
            // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
            if (dismiss === 'cancel') {
              swal({
                title: 'Cancelled',
                type: 'error',
                confirmButtonClass: 'btn btn-info btn-fill',
                buttonsStyling: false
              })
            }
          })
        }
      },
    },//methods
    mounted(){
      this.getUserProfileData()
    }

  };

</script>
<style>

</style>



