<template>
  <div :class="sidebarClasses"
       :data-background-color="backgroundColor"
       :data-active-color="activeColor">
    <div class="logo">
      <a class="simple-text logo-mini"
         href="#">
          <div class="logo-img">
              <img :src="logo" alt="" style="max-width: 35px;top: -1px;">
          </div>
      </a>
      <a class="simple-text logo-normal"
         href="#">
          {{ title }}
      </a>
    </div>
    <div class="sidebar-wrapper" ref="sidebarScrollArea">
      <slot>

      </slot>
      <ul :class="navClasses">
        <slot name="links">
          <sidebar-item v-for="(link, index) in sidebarLinks"
                        :key="link.name + index"
                        :link="link">

            <sidebar-item v-for="(subLink, index) in link.children"
                          :key="subLink.name + index"
                          :link="subLink">
            </sidebar-item>
          </sidebar-item>
        </slot>

      </ul>
    </div>
  </div>
</template>
<script>

import axios from 'axios'
import {mapActions} from 'vuex';
import {mapGetters} from 'vuex'
  export default {
    data(){
      return{
        sidebarLinksLanguage:{
          array:[],
          orignal:[]
        },
        sideBarResponse:{
          parent:{},
          child:{}
        }
      }
    },
    props: {
      title: {
        type: String,
        default: process.env.SIDEBAR_TITLE
      },
      type: {
        type: String,
        default: 'sidebar',
        validator: (value) => {
          let acceptedValues = ['sidebar', 'navbar']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      backgroundColor: {
        type: String,
        default: 'black',
        validator: (value) => {
          let acceptedValues = ['white', 'brown', 'black']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      activeColor: {
        type: String,
        default: 'success',
        validator: (value) => {
          let acceptedValues = ['primary', 'info', 'success', 'warning', 'danger']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      logo: {
        type: String,
        default: process.env.LOGO_IMG
      },
      sidebarLinks: {
        type: Array,
        default: () => []
      }
    },
    computed: {
      sidebarClasses () {
        if (this.type === 'sidebar') {
          return 'sidebar'
        } else {
          return 'collapse navbar-collapse off-canvas-sidebar'
        }
      },
      navClasses () {
        if (this.type === 'sidebar') {
          return 'nav'
        } else {
          return 'nav navbar-nav'
        }
      },
      ...mapGetters([
        'viewLanguageCode',
        'viewNavPanel'
      ])
    },

    methods: {
      testing(){
        console.log("bibi let go")
      },
      setSideBarLinks(){
       this.sidebarLinksLanguage.array = this.$props.sidebarLinks

       // console.log("this.sidebarLinksLanguage.array: ", this.sidebarLinksLanguage.array)

      },
      setSideBarParentNav(){

      const data = {
        "query":"query($set_name:String!,$code:String!,$filters:filters_set_language){view_languages(filters:$filters,set_name:$set_name,code:$code){id,keyword,description,date}}",
        "variables":{
          "set_name":"nav panel",
          "code":this.viewLanguageCode,
          "filters":{
            "limit":20,
            "page":1,
            "sort":"asc",
            "by":"name"
          }
        }
      };
    axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    // console.log("setSideBarParentNav: ",response)
    this.sideBarResponse.parent = response.data.view_languages
    this.sidebarLinksLanguage.orignal = this.viewNavPanel
    // console.log("original:", this.sidebarLinksLanguage.orignal)

      for(var i = 0; i < this.sidebarLinksLanguage.orignal.length; i++){
          for(var j = 0; j < this.sideBarResponse.parent.length; j++){
            if(this.sidebarLinksLanguage.orignal[i].name == this.sideBarResponse.parent[j].keyword){
              this.sidebarLinksLanguage.array[i].name = this.sideBarResponse.parent[j].description
              // console.log("change this")
            }
          }
        }
    
    // console.log("parent response: ",this.sideBarResponse.parent)
    // console.log("parent: ",this.sidebarLinksLanguage.array)



    }).catch((err) => {
    }); 
  },

     setSideBarChildNav(){
      const data = {
        "query":"query($set_name:String!,$code:String!,$filters:filters_set_language){view_languages(filters:$filters,set_name:$set_name,code:$code){id,keyword,description,date}}",
        "variables":{
          "set_name":"navpanel_child",
          "code":this.viewLanguageCode,
          "filters":{
            "limit":20,
            "page":1,
            "sort":"asc",
            "by":"name"
          }
        }
      };

    axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    this.sideBarResponse.child = response.data.view_languages
    this.sidebarLinksLanguage.orignal = this.viewNavPanel

    // console.log("original: ", this.sidebarLinksLanguage.original)

    for(var i = 0; i < this.sidebarLinksLanguage.orignal.length; i++){
      if(this.sidebarLinksLanguage.orignal[i].children.length > 0){
        for(var j = 0; j < this.sidebarLinksLanguage.orignal[i].children.length; j++){
          for(var k = 0;k < this.sideBarResponse.child.length; k++){
            // console.log("yeah",this.sidebarLinksLanguage.orignal[i].children[j].name)
            if(this.sidebarLinksLanguage.orignal[i].children[j].name == this.sideBarResponse.child[k].keyword){
              this.sidebarLinksLanguage.array[i].children[j].name =  this.sideBarResponse.child[k].description
              // console.log("the same")
          }
        }
      }
     }     
    }
    }).catch((err) => {
    });
      
      },

      async initScrollBarAsync () {
        await import('perfect-scrollbar/dist/css/perfect-scrollbar.css')
        const PerfectScroll = await import('perfect-scrollbar')
        PerfectScroll.initialize(this.$refs.sidebarScrollArea)
      }
    },
    watch: {
    'viewLanguageCode': function (newValue, oldValue) {
        console.log("prop, " + this.$props.sidebarLinks)
      if(this.viewLanguageCode == newValue){
       this.setSideBarParentNav()
       this.setSideBarChildNav()

       }
     }
     },

    mounted () {
      this.initScrollBarAsync()
      this.setSideBarLinks()
      this.setSideBarParentNav()
      this.setSideBarChildNav()


    },
    beforeDestroy () {
      if (this.$sidebar.showSidebar) {
        this.$sidebar.showSidebar = false
      }
    }
  };

</script>
<style>
  @media (min-width: 992px) {
    .navbar-search-form-mobile,
    .nav-mobile-menu{
      display: none;
    }
  }
</style>



