<template>
  <div class="languages">
    <div class="row" style="margin-bottom:20px">
      <div class="col-md-3">
        <ul class="list-inline">
          <li>
            <div class="btn-group">
            <button  type="button"  :class="(sortFields.sort =='asc') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsSort('asc')">Ascending</button>
            <button type="button"  :class="(sortFields.sort =='desc') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsSort('desc')">Descending</button>
            </div>
          </li>
          <li>
            <div class="btn-group">
              <button type="button" :class="(sortFields.by=='name') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsBy('name')">Name</button>
            <button type="button" :class="(sortFields.by =='date') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsBy('date')">Date</button>
            </div>
          </li>
          <li>
          <div class="btn-magnify">
             <i class="ti-trash" style="font-size:25px; top:5px;left:5px;cursor:pointer"></i>
           </div>
          </li>
        </ul>
      </div>
      <div class="col-md-3 col-md-offset-5" style="float:right">
      <ul class="list-inline">
       <li>
      <div class="form-group">
      <select @change="getSetName()" v-model="sortFields.pageLimit" class="form-control" style="border-radius: 23px; border: 2px solid; width: 85px; height: 31px;  position: relative;  top: 3px; cursor:pointer; 
     font-size: 12px" >
      <option>20</option>
       <option>50</option> 
       <option>120</option>  
       <option>150</option>  
      </select>
        </div>
        </li>
        <li>     
        <div class="btn-group">
          <button type="button" class="btn btn-default  btn-sm btn-move-left" @click="sortFields.pageNumber > 1 ? sortFields.pageNumber-- : 1,getSetName()">
            <span class="btn-label"> <i class="ti-angle-left"></i> </span>
          </button>
           <button type="button" class="btn btn-default  btn-sm btn-move-left">
            <span class="btn-label">{{sortFields.pageNumber}}</span>
          </button>
          <button type="button" class="btn btn-default btn-sm btn-move-right" @click="sortFields.pageNumber++,getSetName()">
            <span class="btn-label"> <i class="ti-angle-right"></i></span>
          </button>
         </div>
       </li>
        <li>
        <div class="btn-group">
          <button type="button" class="btn btn-default btn-wd btn-sm" @click="openAddFolderModal()">Add Folder</button>
        </div>
        </li>
      </ul>
      </div>
    </div> 
    <!-- Row -->

  <div class="row">       
    <div class="col-md-11" style="margin-top: 20px;">
      <div v-for="items in setNameFields.data" :key="items" >
        <div class="col-md-3" style="position:relative">
          <div class="card">
            <div class="row">
              <div class="col-md-9" style="overflow:hidden">
                <div class="container">
                <router-link :to="{ name: 'Keyword Page', params: { setname: items }}">
                <h4 class="h4Move">{{items}}</h4>
              </router-link>
              </div> 
              </div>
              <div class="col-md-1" style="float:right;margin:20px">
              <div id="icon-container" style="cursor:pointer">
                <div class="btn-magnify">
                <i class="ti-close" @click="deleteFolder(items)" style="color:red"></i>
                </div>
               <div class="btn-magnify">
                  <i class="ti-pencil-alt" @click="openEditFolderModal(items)" style="color:grey"></i>
               </div>
              </div>
            </div>
            </div> 
          </div>
        </div>
       </div>
    </div>
  </div>
<!-- Row -->


<!-- Modal -->
<sweet-modal  ref="addFolderModal"  modal-theme="light"  hide-close-button>
    <div class="card" style="box-shadow:none">
      <div class="card-header" style="text-align: left;">
        <h4 class="card-title" style="margin-bottom: 20px">Add Folder</h4>
        <div class="form-group">
          <input id="addFolderModal" type="text" placeholder="Enter Keyword..." class="form-control" v-model="addFolderFields.setName" autofocus>
        </div>
       </div>
      </div>
      <div  style="float:right">
     <button class="btn btn-md btn-fill btn-primary" @click="saveAddFolderModal()">Save</button>
    </div>
  </sweet-modal>

  <sweet-modal  ref="editFolderModal"  modal-theme="light"  hide-close-button>
    <div class="card" style="box-shadow:none">
      <div class="card-header" style="text-align: left;">
        <h4 class="card-title" style="margin-bottom: 20px">Edit Folder Name</h4>
        <div class="form-group">
          <input id="editFolderModal" type="text" placeholder="Enter Keyword..." class="form-control" v-model="editFolderFields.setName" autofocus>
        </div>
       </div>
      </div>
      <div  style="float:right">
     <button class="btn btn-md btn-fill btn-primary" @click="saveEditFolderModal()">Save</button>
    </div>
  </sweet-modal>
</div>





