</template>
<script>

import Vue from 'vue'
import {Table, TableColumn} from 'element-ui'
import PSwitch from 'src/components/UIComponents/Switch.vue'
import swal from 'sweetalert2'
import axios from 'axios';
import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
import PProgress from 'src/components/UIComponents/Progress.vue'
import { SweetModal, SweetModalTab } from 'sweet-modal-vue'

Vue.use(Table)
Vue.use(TableColumn)

  export default {
  components: {
  [DatePicker.name]: DatePicker,
  [TimeSelect.name]: TimeSelect,
  [Slider.name]: Slider,
  [Tag.name]: Tag,
  [Input.name]: Input,
  [Button.name]: Button,
  [Option.name]: Option,
  [Select.name]: Select,
  PSwitch,
  PProgress,
 'sweet-modal':SweetModal,
'sweet-modal-tab': SweetModalTab
},
data () {
  return {
    addFolderFields:{
      setName:''
    },
    editFolderFields:{
      setName:''
    },
    deleteFolderFields:{
      setName:''
    },

    sortFields: {
      pageNumber: 1,
      pageLimit: 20,
      sort:'asc',
      by:'name'
    },
    setNameFields:{
      data:{}
    },
    selectsSort: {
      simple: 'asc',
      items:[
        {value: 'asc', label: 'Ascending'},
        {value: 'desc', label: 'Descending'},
      ]
    },
     selectsBy: {
      simple2: 'name',
      items2:[
        {value: 'name', label: 'Name'},
        {value: 'date', label: 'Date'},
      ]
    },

     }//return
  },//data
methods: {
  sample(){
    console.log(this.sortFields.pageLimit)
  },
  makeRoute(items){
    console.log("pass this: ",items)
  },

  regexFunction(items){ //later
    if(items.lenght < 5 && items.lenght > 10 ){
      return true;
    }
    return false
  },
  refreshThis(){
      this.$router.go(this.$router.currentRoute)
  },
  sortFieldsSort(items){
    if(items == 'asc'){
      this.sortFields.sort = 'asc'
    }else{
      this.sortFields.sort = 'desc'

    }
    this.getSetName() 
  },
  sortFieldsBy(items){
     if(items == 'name'){
      this.sortFields.by = 'name'
    }else{
      this.sortFields.by = 'date'
    }
    this.getSetName()
  },

  //CRUD
  async addSetName(){
  const data = {
  "query":"mutation($set_name:String!){save_language_set_name(set_name:$set_name)}",
  "variables":{
    "set_name":this.addFolderFields.setName
        }
      }
     return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
     console.log("addsetname: ",response)
        if(response.data.errors){
          return false;
          }else{
            return true;
          }
      });
  },
  getSetName(){
   // console.log("sort: ",this.sortFields.sort)
   // console.log("by: ",this.sortFields.by)
   // console.log("limit: ",this.sortFields.pageLimit)
   // console.log("page: ",this.sortFields.pageNumber)

   const data ={  
     "query":"query($sort:String,$by:String,$limit:Int,$page:Int){view_language_sets(filters:{by:$by,sort:$sort,limit:$limit,page:$page})}",
        "variables":{
            "sort":this.sortFields.sort,
            "by":this.sortFields.by,
            "limit":this.sortFields.pageLimit,
            "page":this.sortFields.pageNumber
        }

    }

    axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    console.log("getSetName: ",response)
    this.setNameFields.data = response.data.view_language_sets
      
      }).catch((err) => {
        return false;
      // console.log(err);
      });
  },
  async deleteSetName(){
   const data = {  
            "query":"mutation($set_name:String!){delete_language_set_name(set_name:$set_name)}",
            "variables":{
                "set_name": this.deleteFolderFields.setName
            }
      }
    return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    console.log("deletesetname: ",response)
    if(response){
      return true
    }else{
      return false
    }
    }).catch((err) => {
    });
  },
  async editSetName(){
    const data = {  
       "query":"mutation($setname_old:String!,$setname_new:String!){update_language_set_name(setname_old:$setname_old,setname_new:$setname_new)}",
          "variables":{
              "setname_old":this.addFolderFields.setName,
              "setname_new":this.editFolderFields.setName
          }
      }
     return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
     console.log("edit setname: ",response)
      if(response){
        return true
      }else{
        return false
      }
      }).catch((err) => {
     
      });
  },
  

 
  //Modals 
  openAddFolderModal(){
    this.$refs.addFolderModal.open()
  },
  openEditFolderModal(items){
    this.addFolderFields.setName = items
    this.editFolderFields.setName = items
    this.$refs.editFolderModal.open()
  },

  async saveAddFolderModal () { 
   if(this.addFolderFields.setName === ''){
      swal({
          type: 'warning',
          html: 'Empty Keyword',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
      });
   }else{
     if(await this.addSetName()){
     this.addFolderFields.setName = ''
    this.getSetName()
    swal({
        type: 'success',
        html: 'Language Added Successfully',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false,
    }).then(()=>{
      this.$refs.addFolderModal.close()
    });
    }else{
      swal({
          type: 'warning',
          html: 'Keyword already exist',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
      });
    }
   }
  },

  async saveEditFolderModal () { 
      if(this.addFolderFields.setName == this.editFolderFields.setName){
        swal({
          type: 'warning',
          html: 'This keyword is the same keyword',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
        })
      }else if(this.editFolderFields.setName == ''){
        swal({
          type: 'warning',
          html: 'Empty Keyword',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
        })
      }else{
          if(await this.editSetName())
          this.getSetName()
          swal({
          type: 'success',
          html: 'You entered',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
        }).then(()=>{
          this.$refs.editFolderModal.close()
        })
      }
      
  },

  async deleteFolder(items) {
    this.deleteFolderFields.setName = items
      swal({
        title: 'Are you sure?',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        buttonsStyling: false
      }).then(async() => {
        if(await this.deleteSetName()){
         swal({
          title: 'Deleted!',
          type: 'success',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false
        }).then(()=>{
          this.getSetName()
        })
      }
      

      }, function (dismiss) {
        if (dismiss === 'cancel') {
          swal({
            title: 'Cancelled',
            type: 'error',
            confirmButtonClass: 'btn btn-info btn-fill',
            buttonsStyling: false
          })
        }
      })
    },
  },
  mounted(){
    this.getSetName()
  },
};
</script>
<style>
.h4Move{
  transition: all .2s ease-in-out;
  cursor:pointer;
}
.h4Move:hover { 
  transform: scale(.98);
  color:red;
}
.select-stlye{

}
</style>




