<template>
  <div class="child-language">
      <div class="row" style="margin-bottom:20px">
      <div class="col-md-5">
        <ul class="list-inline">
          <li>
            <div class="btn-group">
            <button disabled="true"  type="button"  :class="(sortFields.sort =='asc') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsSort('asc')">Ascending</button>
            <button disabled="true" type="button"  :class="(sortFields.sort =='desc') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsSort('desc')">Descending</button>
            </div>
          </li>
          <li>
            <div class="btn-group">
              <button disabled="true" type="button" :class="(sortFields.by=='name') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsBy('name')">Name</button>
            <button disabled="true" type="button" :class="(sortFields.by =='date') ? 'btn btn-fill btn-sm':' btn btn-default btn-sm'"  @click="sortFieldsBy('date')">Date</button>
            </div>
          </li>
          <li>
  
          </li>
          <li>
          <div class="form-group">
          <select @change="getLanguageCode()" v-model="viewSetLanguageFields.vmodel"   class="form-control" style="border-radius: 23px; border: 2px solid; width: 185px; height: 31px;  position: relative;  top: 3px; cursor:pointer; 
         font-size: 10px">
            <option v-for="items in viewSetLanguageFields.data">{{items.full}}</option>
          </select>
          </div>
          </li>
        </ul>
      </div>
      <div class="col-md-3 col-md-offset-4" style="float:right">
      <ul class="list-inline">
       <li>
      <div class="form-group">
      <select disabled="true" @change="getSetName()" v-model="sortFields.pageLimit" class="form-control" style="border-radius: 23px; border: 2px solid; width: 85px; height: 31px;  position: relative;  top: 3px; cursor:pointer; 
     font-size: 12px" >
      <option>20</option>
       <option>50</option> 
       <option>120</option>  
       <option>150</option>  
      </select>
        </div>
        </li>
        <li>     
        <div class="btn-group">
          <button disabled="true" type="button" class="btn btn-default  btn-sm btn-move-left" @click="sortFields.pageNumber > 1 ? sortFields.pageNumber-- : 1,getSetName()">
            <span class="btn-label"> <i class="ti-angle-left"></i> </span>
          </button>
           <button disabled="true" type="button" class="btn btn-default  btn-sm btn-move-left">
            <span class="btn-label">{{sortFields.pageNumber}}</span>
          </button>
          <button disabled="true" type="button" class="btn btn-default btn-sm btn-move-right" @click="sortFields.pageNumber++,getSetName()">
            <span class="btn-label"> <i class="ti-angle-right"></i></span>
          </button>
         </div>
       </li>
        <li>
        <div class="btn-group">
          <button type="button" class="btn btn-default btn-wd btn-sm" @click="openAddKeywordModal()">Add Folder</button>
        </div>
        </li>
      </ul>
      </div>
    </div>
    <!-- Row -->
    <div class="row">
    <div class="col-md-12">
       <div>
      <div class="card">
      <div class="card-header">
        <h2 class="card-title">{{this.$route.params.setname}}</h2>
      </div>  
        <div style="padding:20px;margin:20px" class="table-responsive text-left">
        <table class="table table-bordered table-hover">
          <thead>
            <tr>
              <th width="10%">TRACK#</th>
              <th width="10%">ID#</th>
              <th>Keyword</th>
              <th>Description</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items,index) in getKeywordArray.data">
              <td>
                {{index}}
              </td>   
              <td>
                {{items.id}}
              </td>
              <td>
                {{items.keyword}}
              </td>
               <td v-html="items.description">
              </td>
              <td>
                <button class="btn btn-default btn-sm" @click="openEditKeywordModal(items)">Edit</button>
                <button class="btn btn-default btn-sm" @click="deleteKeywordModal(items)">Delete</button>
              </td>
            </tr>
          </tbody>
        </table>
        </div>   
        </div> 
  
        </div>  
    </div>
    </div>
<!-- Row -->


<!-- Modal -->
<sweet-modal  ref="addKeywordModal"  modal-theme="light" width="50%"  hide-close-button blocking>
    <div class="card" style="margin-top: 30px;box-shadow:none">
      <div class="card-header" style="text-align: left; ">
          <h4 class="card-title" style="margin-bottom: 20px">Add Keyword</h4>
        <div class="form-group">
          <input type="text" class="form-control" v-model="addKeywordFields.keyword" autofocus>
        </div>
      </div>
      <div class="card-content">
        <textarea class="form-control" v-model="addKeywordFields.description"></textarea>
      </div>
      
      </div>
    <div  style="float:right">
    <button class="btn btn-md btn-fill btn-primary" @click="saveAddKeyword()">Save</button>
    <button class="btn btn-md btn-fill btn-danger" @click="closeAddKeywordModal()">Cancel</button>
  </div>
</sweet-modal>

<sweet-modal  ref="editKeywordModal"  modal-theme="light" width="50%"  hide-close-button blocking>
    <div class="card" style="margin-top: 30px;box-shadow:none">
      <div class="card-header" style="text-align: left; ">
          <h4 class="card-title" style="margin-bottom: 20px">Edit Keyword</h4>
        <div class="form-group">
          <input type="text" class="form-control" v-model="editKeywordFields.keyword">
        </div>
      </div>
      <div class="card-content">
        <textarea class="form-control" v-model="editKeywordFields.description"></textarea>
      </div>
      
      </div>
    <div  style="float:right">
    <button class="btn btn-md btn-fill btn-primary" @click="saveEditKeywordModal()">Save</button>
    <button class="btn btn-md btn-fill btn-danger" @click="closeEditKeywordModal()">Cancel</button>
  </div>
</sweet-modal>
</div>
<!-- Language Child -->

</template>
<script>

import Vue from 'vue'
import {Table, TableColumn} from 'element-ui'
import PSwitch from 'src/components/UIComponents/Switch.vue'
import swal from 'sweetalert2'
import axios from 'axios';
import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
import PProgress from 'src/components/UIComponents/Progress.vue'
import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
import { VueEditor } from 'vue2-editor'
Vue.use(Table)
Vue.use(TableColumn)

  export default {
    components: {
    'sweet-modal':SweetModal,
    'sweet-modal-tab': SweetModalTab,
    'vue-editor' :VueEditor,
  },

data () {
  return {
    sortFields: {
      pageNumber: 1,
      pageLimit: 20,
      sort:'asc',
      by:'name'
    },
    addKeywordFields:{
      keyword:'',
      description:''
    },
    deleteKeywordFields:{
      id:'',
      keyword:''
    },
    editKeywordFields:{
      keyword:'',
      description:''
    },
    viewSetLanguageFields:{
      id:'',
      data:'',
      vmodel:'English',
      query:'en'
    },
    getKeywordArray :{
      data:''
    },

  tableData: [],
  saveContent: '<p></p>',


  }//return
},//data
methods: {

  //CRUD  
  setViewLanguage(){ // language code
    //remove sort field
    const data = {  
      "query":"query($limit:Int,$page:Int){view_language_code(filters:{limit:$limit,page:$page}){code,full}}",
          "variables":{
              "limit":150,
              "page":1,
          }
    }
    axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    console.log("set language: ",response)
    this.viewSetLanguageFields.data =  response.data.view_language_code
    // for (var i = 0; i < viewLanguageCode.length; i++) {
    //   this.language.items.push({value:viewLanguageCode[i].code,label:viewLanguageCode[i].full})
    // }
    }).catch((err) => {
          console.log(err)
    });
  },
async getViewLanguage(){
  // console.log("here")
  // console.log("setname: ", this.$route.params.setname)
  // console.log("code: ", this.viewSetLanguageFields.query)
  // console.log("limit: ", this.sortFields.pageLimit)
  // console.log("page: ", this.sortFields.pageNumber)
  // console.log("sort: ", this.sortFields.sort)
  // console.log("by: ", this.sortFields.by)

  const data = {  
    "query":"query($set_name:String!,$code:String!,$filters:filters_set_language){view_languages(filters:$filters,set_name:$set_name,code:$code){id,keyword,description,date}}",
          "variables":{
              "set_name":this.$route.params.setname,
              "code":this.viewSetLanguageFields.query,
              "filters":{
                  "limit":this.sortFields.pageLimit,
                  "page":this.sortFields.pageNumber,
                  "sort":this.sortFields.sort,
                  "by":this.sortFields.by
              }
          }
      }
    return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => { 
      console.log("get language: ", response)
      this.getKeywordArray.data = response.data.view_languages

      if(response.data.errors){
        return false
      }else{
        return true
      }

    }).catch((err) => {
      console.log(err)
    });
  },
  async addKeyword(){
        console.log("description", this.addKeywordFields.description)

    const data = {  
          "query":"mutation($set_name:String!,$keyword:String!,$lang_code:String!,$description:String!){save_language(set_name:$set_name,keyword:$keyword,lang_code:$lang_code,description:$description)}",
            "variables":{
                "set_name":this.$route.params.setname,
                "keyword":this.addKeywordFields.keyword,
                "lang_code":this.viewSetLanguageFields.query,
                "description":this.addKeywordFields.description
            }
        }

      return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("add keyword: ", response)
        if(response.data.errors){
          return true
        }else{
          return false
        }
        
      });
  },
  async saveAddKeyword(){
    if(await this.addKeyword()){
      swal({
        title: 'Error!',
        type: 'warning',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
      }).then(()=>{
        console.log("error for to much loading")
      })
    }else{
      swal({
        title: 'Success!',
        type: 'success',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
      }).then(()=>{
        this.addKeywordFields.keyword = ''
        this.addKeywordFields.description = ''
        this.getViewLanguage()
        this.$refs.addKeywordModal.close()
      });
    }
  },
  async editKeywordModal(){
        const data = {  
          "query":"mutation($set_name:String!,$code:String!,$id:String!,$keyword:String!,$description:String!){update_language_keyword(set_name:$set_name,code:$code,id:$id,keyword:$keyword,description:$description)}",
              "variables":{
                  "id":this.editKeywordFields.id,
                  "set_name":this.$route.params.setname,
                  "keyword":this.editKeywordFields.keyword,
                  "code":this.viewSetLanguageFields.query,
                  "description":this.editKeywordFields.description
              }
        }
      return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log("edit keyword: ",response)

          if(response.data.errors){
            return true
          }else{
            return false
          }
        })
  },

  async saveEditKeywordModal(){
    if(this.editKeywordFields.keyword == '' || this.editKeywordFields.description == ''){

    }
    if(await this.editKeywordModal()){
       swal({
        title: 'Error!',
        type: 'warning',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
      })
    }else{
      swal({
        title: 'Success!',
        text: 'Keyword Edited',
        type: 'success',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
      }).then(async ()=>{
        this.getViewLanguage()
        this.$refs.editKeywordModal.close()
      });
    }
  },


 deleteKeywordModal (items) { 

  console.log(items.id)
  console.log("be hubad")
  this.deleteKeywordFields.id = items.id
  this.deleteKeywordFields.keyword = items.keyword
    swal({
      title: 'Are you sure?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonText: 'Cancel',
      confirmButtonClass: 'btn btn-success btn-fill',
      cancelButtonClass: 'btn btn-danger btn-fill',
      buttonsStyling: false
    }).then(async () => {
      if(await this.deleteKeyword()){
       swal({
          title: 'Something Went Wrong!',
          text: 'Yeah',
          type: 'warning',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false
         })
      }else{
       swal({
        title: 'Deleted!',
        type: 'success',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
       }).then(()=>{
        this.getViewLanguage()
       })
      }
   
    }, function (dismiss) {
      // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
      if (dismiss === 'cancel') {
        swal({
          title: 'Cancelled',
          type: 'error',
          confirmButtonClass: 'btn btn-info btn-fill',
          buttonsStyling: false
        })
      }
    })  
  },


  async deleteKeyword () {
    console.log("setname:  ",this.$route.params.setname)
    console.log("keyword:  ",this.deleteKeywordFields.keyword )
   const data = {  
         "query":"mutation($set_name:String!,$keyword:String!){delete_language_keyword(setname:$set_name,keyword:$keyword)}",
            "variables":{
              "set_name":this.$route.params.setname,
              "keyword":this.deleteKeywordFields.keyword
            }
    }

   return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
    console.log("delete keyword: ",response)
      if(response.data.errors){
        return true
      }else{
        return false
      }
    }).catch((err) => {
   
    });
  },

  //RANDOM
    sortFieldsSort(items){
    if(items == 'asc'){
      this.sortFields.sort = 'asc'
    }else{
      this.sortFields.sort = 'desc'

    }
    this.getViewLanguage() 
  },
  sortFieldsBy(items){
     if(items == 'name'){
      this.sortFields.by = 'name'
    }else{
      this.sortFields.by = 'date'
    }
    this.getViewLanguage() 
  },

  getLanguageCode(){
    console.log(this.viewSetLanguageFields.vmodel)
    for(var i = 0; i < this.viewSetLanguageFields.data.length; i++){
      if(this.viewSetLanguageFields.vmodel == this.viewSetLanguageFields.data[i].full){
       this.viewSetLanguageFields.query = this.viewSetLanguageFields.data[i].code
      }
    }
    this.getViewLanguage() //all language
  },


  duplicateEditContentAndKeyword(){
    this.checkEditKeyword = this.editKeyword
    this.checkEditContent = this.editContent
  },
  backtoLanguagePage() {
   this.$router.push('/languages')
  },

  handleEdit (index, row) {
  this.openEditModal()
  this.editKeyword = row.keyword
  this.editContent = row.description
  this.editId = row.id

  },
  getDeleteVariable(items){
    this.deleteVariable = items.id
  },
  //MODAL TRIGGER
  openAddKeywordModal(){
    this.$refs.addKeywordModal.open()
  },
  openEditKeywordModal(items){
    this.editKeywordFields.keyword = items.keyword
    this.editKeywordFields.description = items.description
    this.editKeywordFields.id = items.id
    this.$refs.editKeywordModal.open()
  },

  closeAddKeywordModal(){
    this.$refs.addKeywordModal.close()
  },
  closeEditKeywordModal(){
    this.$refs.editKeywordModal.close()
  },

  //MODAL
 
 
    pageNumberSwitch(number){
      switch(number) { 
           case "20": { 
              this.pageLimit = 20
              break; 
           } 
           case "50": { 
              this.pageLimit = 50

              break; 
           }
           case "120": { 
              this.pageLimit = 120

              break;    
           } 
           case "150": { 
              this.pageLimit = 150
              break; 
           }  
           default: { 
              this.pageLimit = 20
              break;              
           } 
        } 
    },
    






    refreshThis(){
        this.$router.go(this.$router.currentRoute)
    },

    saveThisModalValidation (type) {
      if (type === 'warning-message-and-cancel') {
      swal({
        title: 'Are you sure?',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        buttonsStyling: false
      }).then(async () => {

        if(await this.saveThisKeywordModal()){
            swal({
            text: 'Records Updated Successfully!',
            type: 'success',
            confirmButtonClass: 'btn btn-success btn-fill',
            buttonsStyling: false
          });
             this.getViewLanguage()
             this.$refs.saveModal.close();
             this.saveKeyword = ""
             this.saveContent = ""
        }else{
            swal({
            text: 'This keyword already exist.',
            type: 'warning',
            confirmButtonClass: 'btn btn-success btn-fill',
            buttonsStyling: false
          })
        }

      }, function (dismiss) {
        // dismiss can be 'overlay', 'cancel', 'close', 'esc', 'timer'
        if (dismiss === 'cancel') {
          swal({
            title: 'Cancelled',
            type: 'error',
            confirmButtonClass: 'btn btn-info btn-fill',
            buttonsStyling: false
          })
        }
      })
    }
  },


 },
 // method
 mounted(){
    this.setViewLanguage()
    this.getViewLanguage()
 }
}
</script>
<style>
</style>



