<template>
  <div class="edit-news">
    <div class="card">
     <ul class="nav nav-tabs">
        <li >
         <router-link to="/news/management" >News Management</router-link>
        </li>
        <li  class="active">
          <router-link to="/news/category" >News Category</router-link>
        </li>
      </ul>

    <div class="tab-content">
    <div class="row">
      <div class="col-md-12">
        <div class="card"  style="box-shadow:none">
         <div class="card-header">
          <div class="row">
            <div class="col-md-4">
               <h2 class="card-title">News Categories</h2>
            </div>
            <div class="col-md-4">
            </div>
            <div class="col-md-4">
              <button class="btn btn-sm" style="float:right;margin-right: 10px;" @click="refreshData()">Refresh</button>
              <button class="btn btn-sm" style="float:right;margin-right: 5px;" @click="openAddCategoryModal()">Add Categories</button>
            </div>
          </div>
          <!-- ROW -->
          </div>
          <div class="row">
            <div class="card-header">
              <div class="form-group">
                <div class="col-md-2">
                  <select class="form-control">
                    <option>Date</option>
                    <option>Status</option>
                    <option>Title</option>
                    <option>Author</option>
                    <option>Category</option>
                  </select>
                </div>
                 <div class="col-md-8">
                  <div class="input-group">
                     <input type="text" class="form-control" placeholder="Please Input" autofocus="true">
                     <span class="input-group-addon"><i class="ti-search"></i></span>
                  </div>
                </div>
                <div class="col-md-2">
                  <button class="btn">Submit</button>
                  <button class="btn">Reset</button>
                </div>
              </div>
              </div>
          </div>
          <hr style="margin-top: 12px;">
          <!-- ROW -->
          <div style="padding:10px;margin:10px; margin-left:10px:width:100%" class="table-responsive">
           <table class="table table-striped table-bordered table-hover">
            <thead class="">
              <tr >
                <th ><span  style="cursor:pointer">Date Created <i style="" :class="(sortableFields.icon.username) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                 <th ><span  style="cursor:pointer">Category Name<i style="" :class="(sortableFields.icon.username) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                <th ><span  style="cursor:pointer">Category Description<i style="" :class="(sortableFields.icon.email) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top: 2px; left: 5px;"></i></span></th>

               <th style="width: 30%;" >Operation</th>
              </tr>
            </thead>
            <tbody v-if="getCategoryFields.status">
              <tr v-for="(items,index) in getCategoryFields.data">
                <td>
                  {{convertDate(items.created)}}
                </td>
                <td>
                  {{items.name}}

                </td>
                <td>
                  {{items.description}}
                </td>
                <td>
                  <button class="btn btn-sm" @click="openViewCategoryModal(items)">View</button>
                  <button class="btn btn-sm" @click="openEditCategoryModal(items)">Edit</button>
                  <button class="btn btn-sm" @click="deleteCategoryModal(items)">Delete</button>
                </td>
              </tr>
            </tbody>
          </table>

            <div  v-if="!getCategoryFields.status">
            <div class="row" style="text-align: center;">
              <i class="fa fa-spinner fa-spin" style="font-size:24px"></i>
            </div>
            </div>
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-3"></div>
              <div class="col-md-2"></div>
               <div class="col-md-1"  style="float:right">
                <div class="form-group">
                  <select class="form-control" @change="getUser()" v-model="sortableFields.limit.pageLimit">
                    <option>7</option>
                    <option>20</option>
                    <option>30</option>
                    <option>50</option>
                    <option>80</option>
                  </select>
                  </div>
               </div>
                <div class="col-md-3" >
               <div class="btn-group" style="float:right;margin-top: 7px;">
                <button type="button" class="btn btn-default  btn-sm btn-move-left" @click="sortableFields.limit.pageNumber > 1 ? sortableFields.limit.pageNumber-- : 1,getUser()">
                  <span class="btn-label"> <i class="ti-angle-left"></i> </span>
                </button>
                 <button type="button" class="btn btn-default  btn-sm btn-move-left">
                  <span class="btn-label">{{sortableFields.limit.pageNumber}}</span>
                </button>
                <button type="button" class="btn btn-default btn-sm btn-move-right" @click="sortableFields.limit.pageNumber++,getUser()">
                  <span class="btn-label"> <i class="ti-angle-right"></i></span>
                </button>
               </div>
              </div>
          </div>
          </div>   
      </div>
      <!-- Card -->
      </div>
      <!-- Column  -->
      </div>
         <!-- ROW -->
         </div>
         <!-- Tab Content -->
    </div>
    <!-- Card -->

 


     <!-- Modal -->
   <sweet-modal  ref="addCategoryModal"  modal-theme="light" width="50%" blocking>
    <div class="card" style="margin-top: 30px; box-shadow:none">
      <div class="card-header" style="text-align: left;">
          <h4 class="card-title" style="margin-bottom: 20px"> Add Category</h4>
        <div class="form-group">
          <input id="addRolesModal" type="text" placeholder="Enter Keyword..." class="form-control" v-model="addCategoryFields.name" autofocus required="">
        </div>
      </div>
      <div class="card-content">
      <div class="form-group">
       <textarea rows="5" class="form-control border-input"
        placeholder="Enter Description" v-model="addCategoryFields.description">    
        </textarea>
        </div>
      </div>
        <div class="card-footer">
        </div>
      </div>
    <div  style="float:right">
    <button class="btn btn-md btn-fill btn-primary" @click="addCategoryModal()">Save</button>
    </div>
  </sweet-modal>


   <sweet-modal  ref="editCategoryModal"  modal-theme="light" width="50%" blocking>
    <div class="card" style="margin-top: 30px; box-shadow:none">
      <div class="card-header" style="text-align: left;">
          <h4 class="card-title" style="margin-bottom: 20px"> Edit Category</h4>
        <div class="form-group">
          <input  type="text" placeholder="Enter Keyword..." class="form-control" v-model="editCategoryFields.name" autofocus required="">
        </div>
      </div>
      <div class="card-content">
      <div class="form-group">
       <textarea rows="5" class="form-control border-input"
        placeholder="Enter Description" v-model="editCategoryFields.description">    
        </textarea>
        </div>
      </div>
        <div class="card-footer">
        </div>
      </div>
    <div  style="float:right">
    <button class="btn btn-md btn-fill btn-primary" @click="editCategoryModal()">Save</button>
    </div>
  </sweet-modal>


  </div>
</template>
<script>
import { VueEditor } from 'vue2-editor'
import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
import {mapFields} from 'vee-validate'
import { SweetModal, SweetModalTab } from 'sweet-modal-vue'

Vue.use(VueTabs)

import VueBase64FileUpload from 'vue-base64-file-upload';
import Datepicker from 'vuejs-datepicker';
import axios from 'axios';
import VueTabs from 'vue-nav-tabs'
import Vue from 'vue'
import swal from 'sweetalert2'

export default {
  name: 'newsfeed',
  computed: {
    ...mapFields(['Title', 'Image','Text','Date','Tags','Author'])
  },
  components: {
    'date-picker': Datepicker,
      'vue-editor' :VueEditor,
    'sweet-modal':SweetModal,
    'sweet-modal-tab': SweetModalTab,
    [DatePicker.name]: DatePicker,
    VueBase64FileUpload
   },
  data () {
  return {   
    refreshValidation:{
      notEqualFields: 0
    },
    sortableFields:{
      fieldsToSort: 'username', 
      icon:{
        username:true,
        email:true,
        created:true
      },
      sort:{
          field:'asc' 
      },
      limit:{
        pageLimit:'7',
        pageNumber:'1',
        lastIdNumber:''
      },
    },
    addCategoryFields:{
      name:'',
      description:''
    },
    editCategoryFields:{
      id:'',
      name:'',
      description:''
    },
    deleteCategoryFields:{
      array:[]
    },
   
    getCategoryFields:{
      data:'',
      status: true
    },


    }
  },
  methods:{

  //RANDOM
  convertDate(date_var){
  var d = new Date(date_var),
  month = '' + (d.getMonth() + 1),
  day = '' + d.getDate(),
  year = d.getFullYear(),
  today = '' + d.getDay().toString();
  var thisDay = "";
  if(today == "1" ){
    thisDay = "Mon"
  }else if(today == "2"){
    thisDay = "Tue"
  }else if(today == "3"){
    thisDay = "Wed"
  }else if(today == "4"){
    thisDay = "Thur"
  }else if(today == "5"){
    thisDay = "Fri"
  }else if(today == "6"){
    thisDay = "Sat"
  }else{
    thisDay = "Sun"
  }
  if (month.length < 2) month = '0' + month;
  if (day.length < 2) day = '0' + day;
  return month + "/"+ day + "/"+ year;
  },

  //CRUD
  getCategory(){
  const data = {
     "query":"query{NewsCategory{id,name,description,created}}"
  }
  axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
  console.log("getCategory: ", response)
  this.getCategoryFields.data = response.data.NewsCategory

  if(this.getCategoryFields.data.length > 0){
    this.getCategoryFields.status = true
    this.addNewsFields.category = this.getCategoryFields.data[0].name
  }else{
    // this.addNewsFields.category = "Null"
  }

  }).catch((err) => {
    console.log(err);
  });
  },

  async addCategory(){
  const data = {
    "query":"mutation($input:newsCategoryInput!){CreateNewsCategory(input:$input){message,errors{Path,Message}}}",
    "variables":{
      "input":{
        "name":this.addCategoryFields.name,
        "description":this.addCategoryFields.description
      }
    }
  }
  return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
  console.log("addCategory: ", response)
  if(response.data.CreateNewsCategory.errors){
    return true
  }else{
    return false
  }

  }).catch((err) => {
    console.log(err);
  });
  },

 async editCategory(){
  const data = {
    "query":"mutation($input:newsCategoryInput!){UpdateNewsCategory(input:$input){message,errors{Path,Message}}}",
    "variables":{
      "input":{
        "id":this.editCategoryFields.id,
        "name":this.editCategoryFields.name,
        "description":this.editCategoryFields.description
      }
    }
  }
  return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
  console.log("editCategory: ", response)
  if(response.data.CreateNewsCategory.errors){
    return true
  }else{
    return false

  }

  }).catch((err) => {
    console.log(err);
  });
  },

  async deleteCategory(){
  const data = {
       "query":"mutation($input:[String!]!){DeleteNewsCategory(input:$input){message,errors{Path,Message}}}",
    "variables":{
      "input": this.deleteCategoryFields.array
    }
  }
  return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
  console.log("deleteCategory: ", response)
  if(response.data.DeleteNewsCategory.errors){
    return true
  }else{
    return false

  }

  }).catch((err) => {
    console.log(err);
  });
  },


  //MODAL CRUD
  async addCategoryModal(){
  if(await this.addCategory()){
    swal({
      title: `Something Went Wrong`,
      buttonsStyling: false,
      type: 'warning',
      confirmButtonClass: 'btn btn-success btn-fill'
      })
    }else{
       swal({
        title: `Success`,
        buttonsStyling: false,
        type: 'success',
        confirmButtonClass: 'btn btn-success btn-fill'
    }).then(()=>{
      this.getCategoryFields.status = false
      this.addCategoryFields.name = " "
      this.addCategoryFields.description = " "
      this.getCategory()
      this.$refs.addCategoryModal.close()
      })
     }
  },

  async editCategoryModal(){
  if(await this.editCategory()){
    swal({
      title: `Something Went Wrong`,
      buttonsStyling: false,
      type: 'warning',
      confirmButtonClass: 'btn btn-success btn-fill'
      })
    }else{
       swal({
        title: `Success`,
        buttonsStyling: false,
        type: 'success',
        confirmButtonClass: 'btn btn-success btn-fill'
    }).then(()=>{
      this.getCategoryFields.status = false
      this.getCategory()
      this.$refs.editCategoryModal.close()
      })
     }
  },

   async deleteCategoryModal(items){
   this.deleteCategoryFields.array.push(items.id)
    swal({
      title: 'Are you sure?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonText: 'Cancel',
      confirmButtonClass: 'btn btn-success btn-fill',
      cancelButtonClass: 'btn btn-danger btn-fill',
      buttonsStyling: false
    }).then(async () => {
    if(await this.deleteCategory())
    {
      swal({
        title: 'Something went wrong!',
        type: 'warning',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
      })
      
    }else{
     swal({
        title: 'News Deleted!',
        type: 'success',
        confirmButtonClass: 'btn btn-success btn-fill',
        buttonsStyling: false
      }).then(()=>{
      this.getCategoryFields.status = false
      this.getCategory()
      })
      }
    }, function (dismiss) {
      if (dismiss === 'cancel') {
        swal({
          title: 'Cancelled',
          type: 'error',
          confirmButtonClass: 'btn btn-info btn-fill',
          buttonsStyling: false
        })
      }
    })
  },

  //MODAL
  openAddCategoryModal(){
    this.$refs.addCategoryModal.open()
  },

    openAddCategoryModal(){
    this.$refs.addCategoryModal.open()
  },

  openEditCategoryModal(items){
    this.editCategoryFields.id = items.id
    this.editCategoryFields.name = items.name
    this.editCategoryFields.description = items.description
    this.$refs.editCategoryModal.open()
  }

  },
  mounted(){
    // this.getNews();`
    this.getCategory()
  }
};
</script>
<style scoped>
  @media(max-width: 1200px) {
    .edit-news{
      /*display: none*/
    }
  }
  .el-input__inner{
    background-color: #F3F2EE !important;
  }
</style>




