<template>
  <div class="token-set">
  <div class="row">
    <div class="card">
      <ul class="nav nav-tabs">
        <li class="active">
         <router-link to="/tokens/airdrop" >Airdrop</router-link>
        </li>
        <li>
          <router-link to="/tokens/bountryprogam">Bounty Program</router-link>
        </li>
         <li >
          <router-link to="/tokens/settings">Token Settings</router-link>
        </li>
      </ul>
      <div class="tab-content">
       <div class="row">
      <div class="col-md-12">
        <div class="card"  style="box-shadow:none">
         <div class="card-header">
          <div class="row">
            <div class="col-md-4">
               <h2 class="card-title">Airdrop</h2>
            </div>
            <div class="col-md-4">
            </div>
            <div class="col-md-4">
            </div>
          </div>
          <!-- ROW -->
          </div>
          <div class="row">
            <div class="card-header">
              <div class="form-group">
                <div class="col-md-2">
                  <select class="form-control">
                    <option>Email</option>
                    <option>User</option>
                  </select>
                </div>
                 <div class="col-md-8">
                  <div class="input-group">
                     <input type="text" class="form-control" placeholder="Please Input" autofocus="true">
                     <span class="input-group-addon"><i class="ti-search"></i></span>
                  </div>
                </div>
                <div class="col-md-1">
                  <button class="btn btn-fill btn-info" >Reset</button>
                </div>
              </div>
              </div>
          </div>
          <hr style="margin-top: 12px;">
          <!-- ROW -->
          <div style="padding:10px;margin:10px" class="table-responsive">
           <table class="table table-striped table-bordered table-hover">
            <thead class="">
              <tr>
                <th ><span  @click="sortData('created')" style="cursor:pointer">Date Created <i style="" :class="(sortableFields.icon.created) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                 <th ><span  @click="sortData('expirationDate')" style="cursor:pointer">Expiration Date <i style="" :class="(sortableFields.icon.expirationDate) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                <th>Status</th>
                <th>Total Supply</th>
                <th>UserID</th>
                <th>Operation</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(items,index) in getAirdropFields.data">
                <td>
                  {{convertDate(items.created)}}
                </td>
                <td>
                  {{convertDate(items.exp)}}
                </td>
                <td>
                  {{items.status}}
                </td>
                <td >
                  {{items.totalSupply}}
                </td>
                <td >
                  <div>
                    NO DATA FROM API
                  </div>
                </td>
                <td>
                  <button class="btn btn-sm" @click="openAirdropUpdateModal(items)">Edit</button>
                  <button class="btn btn-sm" @click="enableAirdropModal(items)"  v-if="!items.status" >Enable</button>
                  <button class="btn btn-sm" @click="disableAirdropModal(items)"  v-if="items.status" >Disable</button>
                </td>
              </tr>
            </tbody>
          </table>
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-3"></div>
              <div class="col-md-2"></div>
               <div class="col-md-1"  style="float:right">
                <div class="form-group">
                  <select class="form-control" @change="getUser()" v-model="sortableFields.limit.pageLimit">
                    <option>7</option>
                    <option>20</option>
                    <option>30</option>
                    <option>50</option>
                    <option>80</option>
                  </select>
                  </div>
               </div>
                  <div class="col-md-3" >
               <div class="btn-group" style="float:right;margin-top: 7px;">
                <button type="button" class="btn btn-default  btn-sm btn-move-left" @click="sortableFields.limit.pageNumber > 1 ? sortableFields.limit.pageNumber-- : 1,getUser()">
                  <span class="btn-label"> <i class="ti-angle-left"></i> </span>
                </button>
                 <button type="button" class="btn btn-default  btn-sm btn-move-left">
                  <span class="btn-label">{{sortableFields.limit.pageNumber}}</span>
                </button>
                <button type="button" class="btn btn-default btn-sm btn-move-right" @click="sortableFields.limit.pageNumber++,getUser()">
                  <span class="btn-label"> <i class="ti-angle-right"></i></span>
                </button>
               </div>
              </div>
          </div>
          </div>   
      </div>
        <!-- Card -->
      </div>
      <!-- Column  -->
    </div>
      </div>
    </div> 
  </div>
  <!-- Row -->
    <sweet-modal ref="airdropUpdateModal"  modal-theme="light">
      <div class="card" style="box-shadow:none">
    <div class="card-header">
      <h4 class="title">Update Airdrop</h4>
      <hr> 
    </div>
    <div class="card-content">
      <form>
          <div class="row">
          <div class="col-md-6">
                <label style="">Expiration Date</label> <br/>
              <el-date-picker v-model="airdropUpdateFields.expirationDate" type="date"  placeholder="Pick a day"
                :picker-options="pickerOptions1">
              </el-date-picker>
          </div>
          <div class="col-md-6">
          <fg-input type="Total"label="Supply" placeholder="Email" v-model="airdropUpdateFields.totalSupply">
          </fg-input>
          </div>
        
        </div>
      </form>
        <div style="margin-top:50px">
          <button class="btn" style="float:right" @click="updateAirdrop()">Save</button>
         </div>
    </div>
  </div>
  </sweet-modal>
</div>
</template>
<script>

import axios from 'axios'
import swal from 'sweetalert2'
import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
import Vue from 'vue'

export default {
  components:{
   'sweet-modal':SweetModal,
   'sweet-modal-tab': SweetModalTab,
   [DatePicker.name]: DatePicker,
  },
  data () {
    return {
        pickerOptions1: {
          shortcuts: [{
            text: 'Today',
            onClick (picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: 'Yesterday',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: 'A week ago',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }]
        },
      sortableFields:{
        fieldsToSort: 'username', 
        icon:{
          created:true,
          expirationDate:true,
          totalSupply:true
        },
        sort:{
          field:'asc' 
        },
        limit:{
          pageLimit:'7',
          pageNumber:'1',
          lastIdNumber:''
        }
      },
      getAirdropFields:{
        data:''
      },
      airdropUpdateFields:{
        totalSupply:'',
        expirationDate:''
      },
      airdropEnableFields:{
        id:''
      },
     airdropDisableFields:{
        id:''
      }


    }
  },
  methods: {
    //SORT
    sortData(items){
      this.sortableFields.fieldsToSort = items

      switch (this.sortableFields.fieldsToSort) {
        case 'created':
          if(this.sortableFields.icon.created){
            this.sortableFields.icon.created = false
            this.sortableFields.sort.field = 'desc'
            this.getAirdrop()
          }else{
            this.sortableFields.icon.created = true
            this.sortableFields.sort.field = 'asc'
            this.getAirdrop()
          }
          break;
      case 'fullname':
        if(this.getAirdrop.icon.fullname){
          this.sortableFields.icon.fullname = false
          this.sortableFields.sort.field = 'desc'
          this.getAirdrop()
        }else{
          this.sortableFields.icon.fullname = true
          this.sortableFields.sort.field = 'asc'
          this.getAirdrop()
        }
        break;
      default:
        break;
      }
    },
    //CONVERT
    convertDate(date_var){
      var d = new Date(date_var),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear(),
      today = '' + d.getDay().toString();
      if (month.length < 2) month = '0' + month;
      if (day.length < 2) day = '0' + day;
      return month + "/"+ day + "/"+ year;
    },

    convertFormatedDate(date_var){
      var d = new Date(date_var),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear(),
      today = '' + d.getDay().toString();
      if (month.length < 2) month = '0' + month;
      if (day.length < 2) day = '0' + day;
      return month + "."+ day + "."+ year;
    },

    //CRUD

    updateAirdrop(){
      const data = {
        "query":"mutation($id:String!,$input:airdropDetails!){updateAirdrop(id:$id,input:$input){ message,errors { Path,Message } } }",
        "variables":{
          "id":"a5ba9d94b359e7c27a89586ada",
          "input":{
              "expDate":this.airdropUpdateFields.expirationDate,
              "totalSupply": this.convertFormatedDate(this.airdropUpdateFields.expirationDate)
          }
        }
      }
      axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("updateAirdrop: ", response)
      }).catch((err) => {
        console.log(err);
      });
    },

    getAirdrop(){
      const data = {
        "query":"query($limit:Limit,$sort:sort,$search:search){getAirdrop(limit:$limit,sort:$sort,search:$search){ data{ id userID exp totalSupply created status } page { items pageNo totalPages} sortableFields searchableFields }}",
        "variables":{
            "limit":{
                "to":7,
                "page":1
            },
            "sort":{
              "field":this.sortableFields.fieldsToSort,
              "by": this.sortableFields.sort.field
            },
            "search":{
                "by":"",
                "text":""
            }
        }
      }
      axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("getAirdrop: ", response)
      this.getAirdropFields.data = response.data.getAirdrop.data
      }).catch((err) => {
        console.log(err);
      });
    },
    async enableAirdrop(){
      const data = {
       "query":"mutation($id:String!){enableAirdrop(id:$id){ message errors{Path Message}}}",
       "variables":{
            "id":this.airdropEnableFields.id
        }
      }
      return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("enableAirdrop: ", response)
      if(response.data.enableAirdrop.errors){
        return true
      }else{
        return false
      }
      }).catch((err) => {
        console.log(err);
      });
    },
    disableAirdrop(){
      const data = {
        "query":"mutation($id:String!){disableAirdrop(id:$id){ message errors{Path Message}}}",
        "variables":{
            "id":this.airdropDisableFields.id
        }
      }
      axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("disableAirdrop: ", response)
      }).catch((err) => {
        console.log(err);
      });
    },

    //Modal
    openAirdropUpdateModal(items){
      console.log("update modal", items.exp)
      this.airdropUpdateFields.expirationDate = items.exp
      this.airdropUpdateFields.totalSupply = items.totalSupply
      this.$refs.airdropUpdateModal.open()
    },

    async enableAirdropModal(items){
     this.airdropEnableFields.id = items.id
      if(await this.enableAirdrop()){
      swal({
        title: `Something Went Wrong`,
        buttonsStyling: false,
        type: 'warning',
        confirmButtonClass: 'btn btn-success btn-fill'
        })
      }else{
         swal({
          title: `Success`,
          buttonsStyling: false,
          type: 'success',
          confirmButtonClass: 'btn btn-success btn-fill'
      }).then(()=>{
        this.getAirdrop()
      })
     }
    },
    async disableAirdropModal(items){
     this.airdropDisableFields.id = items.id
      if(await this.disableAirdrop()){
      swal({
        title: `Something Went Wrong`,
        buttonsStyling: false,
        type: 'warning',
        confirmButtonClass: 'btn btn-success btn-fill'
        })
      }else{
         swal({
          title: `Success`,
          buttonsStyling: false,
          type: 'success',
          confirmButtonClass: 'btn btn-success btn-fill'
      }).then(()=>{
        this.getAirdrop()
      })
     }
    }
  },
  mounted(){
   this.getAirdrop()
  }
};
</script>
<style>
</style>



