<template>
  <div class="all-user">
    <div class="row">
      <div class="col-md-12">
        <div class="card">
         <div class="card-header">
          <div class="row">
            <div class="col-md-4">
               <h2 class="card-title">User Management</h2>
            </div>
            <div class="col-md-4">
            </div>
            <div class="col-md-4">
              <button class="btn btn-sm" style="float:right;margin-right: 10px;" @click="refreshData()">Refresh</button>
              <button class="btn btn-sm" style="float:right;margin-right: 5px;" @click="openAddUserModal()">Add</button>
            </div>
          </div>
          <!-- ROW -->
          </div>
          <div class="row">
            <div class="card-header">
              <div class="form-group">
                <div class="col-md-2">
                  <select class="form-control">
                    <option>Email</option>
                    <option>User</option>
                  </select>
                </div>
                 <div class="col-md-8">
                  <div class="input-group">
                     <input type="text" class="form-control" placeholder="Please Input" autofocus="true">
                     <span class="input-group-addon"><i class="ti-search"></i></span>
                  </div>
                </div>
                <div class="col-md-1">
                  <button class="btn btn-fill btn-info" >Reset</button>
                </div>
              </div>
              </div>
          </div>
          <hr style="margin-top: 12px;">
          <!-- ROW -->
          <div style="padding:10px;margin:10px" class="table-responsive">
           <table class="table table-striped table-bordered table-hover">
            <thead class="">
              <tr>
                <th ><span @click="sortData('username')" style="cursor:pointer">Username <i style="" :class="(sortableFields.icon.username) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top:2px; left: 5px;"></i></span></th>
                <th ><span @click="sortData('email')" style="cursor:pointer">Email</span><i style="" :class="(sortableFields.icon.email) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top: 2px; left: 5px;"></i></th>
                   <th><span @click="sortData('created')" style="cursor:pointer">Created</span><i style="" :class="(sortableFields.icon.created) ? 'ti-angle-double-up':'ti-angle-double-down'" style="position: relative;top: 2px; left: 5px;"></i></th>
                <th >Roles</th>
                <th >Language</th>
             
                <th >Operation</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(items,index) in userData.users">
                <td >
                  {{checkIfGoogleRegistered(items.Username)}}
                </td >
                <td>
                  {{items.Email}}
                </td>
                    <td>
                  {{convertDate2(items.Created)}}
                </td>
                <td >
                  <div v-for="(items,index) in items.Roles">
                   {{items}}
                  </div>
                </td>
                <td>
                  {{items.Language}}
                </td>
          
                <td>
                  <button class="btn btn-sm" @click="openPreviewModal(items)">View</button>
                  <button class="btn btn-sm" @click="openAddRolesModal(items)">Edit</button>
                  <button class="btn btn-sm">Disable</button>
                </td>
              </tr>
            </tbody>
          </table>
            <div class="row">
              <div class="col-md-3"></div>
              <div class="col-md-3"></div>
              <div class="col-md-2"></div>
               <div class="col-md-1"  style="float:right">
                <div class="form-group">
                  <select class="form-control" @change="getUser()" v-model="sortableFields.limit.pageLimit">
                    <option>7</option>
                    <option>20</option>
                    <option>30</option>
                    <option>50</option>
                    <option>80</option>
                  </select>
                  </div>
               </div>
                  <div class="col-md-3" >
               <div class="btn-group" style="float:right;margin-top: 7px;">
                <button type="button" class="btn btn-default  btn-sm btn-move-left" @click="sortableFields.limit.pageNumber > 1 ? sortableFields.limit.pageNumber-- : 1,getUser()">
                  <span class="btn-label"> <i class="ti-angle-left"></i> </span>
                </button>
                 <button type="button" class="btn btn-default  btn-sm btn-move-left">
                  <span class="btn-label">{{sortableFields.limit.pageNumber}}</span>
                </button>
                <button type="button" class="btn btn-default btn-sm btn-move-right" @click="sortableFields.limit.pageNumber++,getUser()">
                  <span class="btn-label"> <i class="ti-angle-right"></i></span>
                </button>
               </div>
              </div>
          </div>
          </div>   
      
      </div>
        <!-- Card -->
      </div>
      <!-- Column  -->
    </div>
    <!-- Row  -->

   <sweet-modal  ref="addUserModal"  modal-theme="light" blocking>
       <div class="form-horizontal">
        <!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
        <div class=" card-content">
          <div>
            <div class="row">
              <div class="col-md-11">
                <form method="#" action="#">
                  <div class="card card-plain">
                    <div class="content">
                      <div class="form-group">
                        <label class="col-sm-3 control-label" style="margin-top:-5px">Username</label>
                        <div class="col-sm-9">
                          <input type="text" name="Title" class="form-control" placeholder="Username" v-model="userFields.username">
                        </div>
                      </div>
                         <div class="form-group">
                        <label class="col-sm-3 control-label" style="margin-top:-5px">Password</label>
                        <div class="col-sm-9">
                          <input type="password" name="Title" class="form-control" placeholder="Password"  v-model="userFields.password">
                        </div>
                      </div>
                         <div class="form-group">
                        <label class="col-sm-3 control-label" style="margin-top:-5px">Email</label>
                        <div class="col-sm-9">
                          <input type="email" name="Title" class="form-control" placeholder="Email"  v-model="userFields.email">
                        </div>
                      </div>
                         <div class="form-group">
                        <label class="col-sm-3 control-label" style="margin-top:-5px">Firstname</label>
                        <div class="col-sm-9">
                          <input type="text" name="Title" class="form-control" placeholder="Firstname"  v-model="userFields.firstname">
                        </div>
                      </div>
                       <div class="form-group">
                        <label class="col-sm-3 control-label" style="margin-top:-5px">Lastname</label>
                        <div class="col-sm-9">
                          <input type="text" name="Title" class="form-control" placeholder="Lastname"  v-model="userFields.lastname">
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    <div class="footer">
      <button style="float:right" class="btn btn-info btn-fill" @click="registerUserModal()">Save</button>
    </div>
  </sweet-modal>
  <sweet-modal ref="previewUserModal"width="70%" style="margin-left:120px"  modal-theme="light" blocking>
      <div class="card" style="box-shadow:none">
    <div class="card-header">
      <h4 class="title">View User Profile</h4>
    </div>
    <div class="card-content">
      <form>
        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="Username" 
                      placeholder="Username" 
                      v-model="userProfilePage.username"
                      disabled>
            </fg-input>
          </div>
          <div class="col-md-4">
          <fg-input type="email"
                    label="Email"
                    placeholder="Email" 
                    v-model="userProfilePage.email"
                      disabled>
          </fg-input>
        
          </div>
          <div class="col-md-4">
            <fg-input type="password"
                      label="Password"
                      placeholder="password"
                      value="password"
                        disabled>
          </fg-input>
     
          </div>
        </div>
        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="First Name"
                      placeholder="First Name"
                      v-model="userProfilePage.firstname"
                        disabled>
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Last Name"
                      placeholder="Last Name"
                      v-model="userProfilePage.lastname"
                        disabled>
            </fg-input>
          </div>
            <div class="col-md-4">
            <div class="form-group">
               <fg-input type="text"
                label="Birthdate"
                placeholder="Birthdate"
                v-model="userProfilePage.birthdate"
                disabled>
            </fg-input>
            </div>
          </div>
        </div>
        <div class="row">
        <div class="col-md-4">
            <fg-input type="text"
                      label="City"
                      placeholder="City"
                      v-model="userProfilePage.city"
                        disabled>
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Country"
                      placeholder="Country"
                      v-model="userProfilePage.country" disabled>
                     >
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Street"
                      placeholder="Street"
                      v-model="userProfilePage.street"
                      disabled>
                      >
            </fg-input>
          </div>
        </div>

          <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="Roles"
                      placeholder="Roles"
                      v-model="userProfilePage.roles" disabled>
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Status"
                      placeholder="Country"
                      v-model="userProfilePage.status" disabled>
                     >
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="UserID"
                      placeholder="User ID"
                      v-model="userProfilePage.userid" disabled> 
            </fg-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="Created"
                      placeholder="Created"
                      v-model="userProfilePage.created" disabled> 
            </fg-input>
          </div>
           <div class="col-md-4">
            <fg-input type="text"
                      label="Language"
                      placeholder="Language"
                      v-model="userProfilePage.language" disabled> 
            </fg-input>
          </div>
        </div>
      </form>
    </div>
  </div>
  </sweet-modal>
  <sweet-modal  ref="addRolesModal"  modal-theme="light"  style="margin-left: 120px;" blocking>
    <div class="row" style="margin-left: 49px">
      <div class="col-md-11">
      <div class="card" style="box-shadow:none">
        <div class="card-header">
         <h4 class="card-title">
           {{modalData.items.Email}}
         </h4>
         <hr>
         <div v-for="(items,index) in roles.data">
          <ul class="list-inline">
              <li>
                {{items.Name}} : <input type="checkbox" :id="items.Name" :value="items.Name" v-model="modalData.selected">
              </li>
          </ul>
         </div>
         <hr>
         <div class="card-footer">
           <button class="btn btn-fill btn-primary" style="float:right" @click="saveThisUserRolesModal()">Save</button>
         </div>
        </div>
      </div><!--card-->
      </div>
    </div>
  </sweet-modal>
  </div>
  <!--all-user-->
</template>
<script>
  import { SweetModal, SweetModalTab } from 'sweet-modal-vue'
  import swal from 'sweetalert2'
  import Vue from 'vue'
  import axios from 'axios'
  import {Table, TableColumn} from 'element-ui'
  import PSwitch from 'src/components/UIComponents/Switch.vue'
  import StatsCard from 'src/components/UIComponents/Cards/StatsCard.vue'
  import ElFormItem from 'element-ui/packages/form/src/form-item'
  import {DatePicker, TimeSelect, Slider, Tag, Input, Button, Select, Option} from 'element-ui'
  Vue.use(Table)
  Vue.use(TableColumn)
  export default {
    components: {
    StatsCard,
    ElFormItem,
    [DatePicker.name]: DatePicker, 
    'sweet-modal':SweetModal,
    'sweet-modal-tab': SweetModalTab
    },
    data () {
      return {
        pagination:[],
        sortableFields:{
            fieldsToSort: 'username', 
          icon:{
            username:true,
            email:true,
            created:true
          },
            sort:{
              field:'asc' 
            },
            limit:{
              pageLimit:'7',
              pageNumber:'1',
              lastIdNumber:''
            },
          },
        userProfilePage:{
          username:'',
          email:'',
          birthdate:'',
          firstname:'',
          lastname:'',
          city:'',
          country:'',
          street:'',
          status:'',
          userid:'',
          created:'',
          language:'',
          roles:'',
        },
        roles:{
          data:{}
        },
        modalData:{
          items:{},
          selected:[]
        },
          tempUser:{},
        tableHead:{
          username: true,
          roles: true,
          email: true,
          firstname: true,
          lastname: true,
          birthdate: true,
          language: true,
          created : true,
          actions : true
        },
        userData : {
          users: {}
        },
          userFields:{
            username:'',
            password:'',
            email:'',
            firstname:'',
            lastname:''
          },
          modelValidations: {
          username: {
            required: true
          },
          password: {
            required: true,
            min: 5
          },
          email: {
            required: true
          }
          },


          pickerOptions1: {
            shortcuts: [{
              text: 'Today',
              onClick (picker) {
                picker.$emit('pick', new Date())
              }
            },
            {
              text: 'Yesterday',
              onClick (picker) {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24)
                picker.$emit('pick', date)
              }
            },
            {
              text: 'A week ago',
              onClick (picker) {
                const date = new Date()
                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
                picker.$emit('pick', date)
              }
            }]
          }
      }
    },
    methods: {
      //ETC METHOD
      getUserLastId(items){
        var limit = items.length-1;
        var lastUser = items[limit]
        this.sortableFields.limit.lastIdNumber = lastUser.UserID



      },
      refreshData(){
          this.sortableFields.fieldsToSort = 'username'
          this.sortableFields.icon.username = true
          this.sortableFields.icon.email = true
          this.sortableFields.icon.created = true
          this.sortableFields.sort.field = 'asc'
          this.sortableFields.limit.pageLimit = '7'
          this.sortableFields.limit.pageNumber = '1'
          this.sortableFields.limit.lastIdNumber = ''
          this.getUser()
      },
      //SORT
      sortData(items){
        this.sortableFields.fieldsToSort = items

        switch (this.sortableFields.fieldsToSort) {
              case 'username':
                   if(this.sortableFields.icon.username){
                      this.sortableFields.icon.username = false
                      this.sortableFields.sort.field = 'desc'
                      this.getUser()
                    }else{
                      this.sortableFields.icon.username = true
                      this.sortableFields.sort.field = 'asc'
                      this.getUser()
                    }
                  break;
              case 'email':
                    if(this.sortableFields.icon.email){
                      this.sortableFields.icon.email = false
                      this.sortableFields.sort.field = 'desc'
                      this.getUser()
                    }else{
                      this.sortableFields.icon.email = true
                      this.sortableFields.sort.field = 'asc'
                      this.getUser()
                    }
                  break;
              case 'created':
                      if(this.sortableFields.icon.created){
                      this.sortableFields.icon.created = false
                      this.sortableFields.sort.field = 'desc'
                      this.getUser()
                    }else{
                      this.sortableFields.icon.created = true
                      this.sortableFields.sort.field = 'asc'
                      this.getUser()
                    }
                  break;
              default:
                  break;
          }
      },

      //MODAL
      async saveThisUserRolesModal (type) {
        if(await  this.saveThisUserAddRoles()){
          swal({
            title: 'Something went wrong!',
            text: 'Your imaginary file has been edited.',
            type: 'warning',
            confirmButtonClass: 'btn btn-success btn-fill',
            buttonsStyling: false
          }).then(()=>{
            this.$refs.addRolesModal.close()

          })
        }else{
          this.getUser()
          swal({
            title: 'Filed edited!',
            text: 'Your imaginary file has been edited.',
            type: 'success',
            confirmButtonClass: 'btn btn-success btn-fill',
            buttonsStyling: false
          });
        }
      }, 

      //CRUD
      async saveThisUserAddRoles(){
        const data = {
            "query":"mutation($Roles:[String!]!,$UserID:String!) { UpdateUserRole( Roles:$Roles,UserID:$UserID ) { message,errors { Path,Message } } }",
            "variables":{
              "Roles":this.modalData.selected,
              "UserID":this.modalData.items.UserID
          }
        }
       return await axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
      console.log("add user roles",response)
        if(response.data.UpdateUserRole.errors.length > 0){
          return true
        }else{
          return false
        }

        }).catch((err) => {
        });
      },
      getUser(){
        const data = {
         "query":"query ($limit:limit,$sort:sort) { users (limit:$limit,sort:$sort) { data { UserID,Username,Firstname,Lastname,Address { Country,City,Street } ,Email,Roles,Birthdate,Language,Status,Created }, page { items,pageNo,totalPages },sortableFields }}",
          "variables":{
            "sort":{
              "field":this.sortableFields.fieldsToSort,
              "by":this.sortableFields.sort.field,
            },
            "limit":{
              "to":this.sortableFields.limit.pageLimit,
            "page":this.sortableFields.limit.pageNumber,
            "last_id":this.pagination[this.pagination.length -1]
            }
          }
        }

        axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log("getusers: ",response)
        this.userData.users = response.data.users.data
        this.getUserLastId(this.userData.users)
          
          this.tempUser = this.userData.users[0]
        }).catch((err) => {
        });
        
      },

      getRolesPermission(){
        const data = {
           "query":"query{ roles { data{ RoleID,Name,Description,Read,Update,Create,Delete,View,Created },page { items,pageNo,totalPages } ,sortableFields } }"
        }
        axios.post(this.post_url,JSON.stringify(data),this.post_config_auth).then((response) => {
        console.log("roles Permission, ",response)
        this.roles.data = response.data.roles.data


        }).catch((err) => {
        });
      },
       async registerUserModal(){
        if(await this.registerUser()){
         swal({
          type: 'warning',
          html: 'Something went wrong',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
        })
        }else{
        swal({
          type: 'success',
          html: 'Registration Success',
          confirmButtonClass: 'btn btn-success btn-fill',
          buttonsStyling: false,
        }).then(()=>{
          this.userFields.username = ''
          this.userFields.password = ''
          this.userFields.email = ''
          this.userFields.firstname = ''
          this.userFields.lastname = ''
          this.$refs.addRolesModal.close()
          this.getUser()
        })
        }
      },
      async registerUser(){
        const data = {
                "query":"mutation($input:register!) { NewUser( input:$input ) { token,exp,data { Firstname,Lastname,id,Email,Language } ,errors {Message,Path} } }",
                "variables":{
                  "input":{
                     "username":this.userFields.username,
                        "password":this.userFields.password,
                        "email":this.userFields.email,
                        "firstname":this.userFields.firstname,
                        "lastname":this.userFields.lastname
                  }
                }
             }
          return await axios.post(this.post_url,JSON.stringify(data),this.post_config).then((response) => {
          console.log("add user")
            if(response.data.errors){
              return true
            }else{
              return false
            }
            }).catch((err) => {
            });

      },
      checkIfGoogleRegistered(items){
        if(items == null){
          return 'Registered By Google'
        }else{
          return items
        }
      },
    
      openAddUserModal(){
        this.$refs.addUserModal.open()
      },
      openPreviewModal(items){
        this.$refs.previewUserModal.open()
        this.userProfilePage.username = items.Username
        this.userProfilePage.birthdate = items.Birthdate
        this.userProfilePage.email = items.Email
        this.userProfilePage.language = items.Language
        this.userProfilePage.firstname = items.Firstname
        this.userProfilePage.lastname = items.Lastname
        this.userProfilePage.userid = items.UserID
        // this.userProfilePage.roles = items.Roles
        var rolesString
        for(var i = 0; i < items.Roles.length; i++){
          rolesString = items.Roles[i] + " "
        }
        this.userProfilePage.roles = rolesString

        items.Status ? this.userProfilePage.status = 'active' :  this.userProfilePage.status = 'inactive'  
        items.Created ? this.userProfilePage.created = this.convertDate2(items.Created) :  this.userProfilePage.created = 'false'  

      },
      openAddRolesModal(items){
        this.$refs.addRolesModal.open()
        this.modalData.selected = [] //set to 0 to reset shit
        this.modalData.items = items
        this.pushModalDataRoles(items.Roles)
      },
      pushModalDataRoles(items){
        for(var i = 0; i < items.length; i++){
          this.modalData.selected.push(items[i])
        }
      },
     convertDate2(date_var){
      if(date_var == null){
        return false
      }else{
           var d = new Date(date_var),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear(),
        today = '' + d.getDay().toString();

      var thisDay = "";
        if(today == "1" ){
          thisDay = "Mon"
        }else if(today == "2"){
          thisDay = "Tue"
        }else if(today == "3"){
          thisDay = "Wed"
        }else if(today == "4"){
          thisDay = "Thur"
        }else if(today == "5"){
          thisDay = "Fri"
        }else if(today == "6"){
          thisDay = "Sat"
        }
         else {
          thisDay = "Sun"
        }
        if (month.length < 2) month = '0' + month;
        if (day.length < 2) day = '0' + day;

        return month + "/"+ day + "/"+ year + " ";
          }
       },
     },//methods
     mounted(){
      this.getUser()
      this.getRolesPermission()
     }
  }

</script>
<style>
</style>


